import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { 
    getNotificationPreferencesService,
    updateNotificationPreferencesService,
    getNotificationFeedService,
    markNotificationReadService,
    markAllNotificationsReadService,
    deleteNotificationService,
    clearAllNotificationsService
} from './services';
import type { 
    NotificationPreference, 
    NotificationFeedResponse 
} from './types';
import { handleActionResponse } from '../global/actions';

export const getNotificationPreferencesAction = createAsyncThunk<
    NotificationPreference,
    void,
    { rejectValue: string }
>(
    'notifications/getPreferences',
    async (_, { rejectWithValue }) => {
        try {
            const response = await getNotificationPreferencesService();
            const result = handleActionResponse<NotificationPreference>(
                response,
                'Fetched notification preferences',
                'Failed to fetch notification preferences'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage = error instanceof Error 
                ? error.message 
                : 'Failed to fetch notification preferences';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

