import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import {
    getReferralRewardHistoryService,
    getReferralLeaderboardService,
    getReferralStatsService,
    listAllReferralRewardsService,
    creditReferralRewardService,
    revokeReferralRewardService
} from './services';
import type {
    ReferralRewardHistory,
    ReferralLeaderboardItem,
    ReferralStats,
    ListAllReferralRewardsResponse,
    ListAllReferralRewardsQueries,
    CreditReferralRewardRequest,
    RevokeReferralRewardRequest
} from './types';

// User: get referral reward history
export const getReferralRewardHistoryAction = createAsyncThunk<
    ReferralRewardHistory[],
    void,
    { rejectValue: string }
>(
    'referrals/getReferralRewardHistory',
    async (_, { rejectWithValue }) => {
        try {
            const response = await getReferralRewardHistoryService();
            const result = handleActionResponse<{ history: ReferralRewardHistory[] }>(
                response,
                'Fetched referral reward history',
                'Failed to fetch referral reward history'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result.history;
        } catch (error: unknown) {
            const errorMessage = error instanceof Error ? error.message : 'Failed to fetch referral reward history';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

