import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import type { ApiError, ActionResponse } from '../global/types';
import type {
    ApiKeyListResponse,
    ApiKeyRequest,
    ApiKeyUsageResponse,
    ApiKeyTestResponse
} from './types';

export const getApiKeysService = async (
    queries?: { page?: string; limit?: string; }
): Promise<ActionResponse<ApiKeyListResponse>> => {
    try {
        const response = await makeApiCall<ApiKeyListResponse>(
            '/api/keys',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const addApiKeyService = async (
    data: ApiKeyRequest
): Promise<ActionResponse<{ success: boolean }>> => {
    try {
        const response = await makeApiCall<{ success: boolean }>(
            '/api/keys',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const getApiKeyUsageService = async (
    keyId: string
): Promise<ActionResponse<ApiKeyUsageResponse>> => {
    try {
        const response = await makeApiCall<ApiKeyUsageResponse>(
            `/api/keys/${keyId}/usage`,
            'GET'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const deleteApiKeyService = async (
    keyId: string
): Promise<ActionResponse<{ success: boolean }>> => {
    try {
        const response = await makeApiCall<{ success: boolean }>(
            `/api/keys/${keyId}`,
            'DELETE'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const testApiKeyService = async (
    data: ApiKeyRequest
): Promise<ActionResponse<ApiKeyTestResponse>> => {
    try {
        const response = await makeApiCall<ApiKeyTestResponse>(
            '/api/keys/test',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};
