import { createReducer } from '@reduxjs/toolkit';
import {
    getReferralRewardHistoryAction,
    getReferralLeaderboardAction,
    getReferralStatsAction,
    listAllReferralRewardsAction,
    creditReferralRewardAction,
    revokeReferralRewardAction
} from './actions';
import type { ReferralsState } from './types';

const initialState: ReferralsState = {
    isLoading: false,
    error: undefined,
};

export default createReducer(initialState, (builder) => {
    builder
        .addCase(getReferralRewardHistoryAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getReferralRewardHistoryAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getReferralRewardHistoryAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
        .addCase(getReferralLeaderboardAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getReferralLeaderboardAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getReferralLeaderboardAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
        .addCase(getReferralStatsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getReferralStatsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getReferralStatsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
        .addCase(listAllReferralRewardsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(listAllReferralRewardsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(listAllReferralRewardsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
        .addCase(creditReferralRewardAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(creditReferralRewardAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(creditReferralRewardAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        })
        .addCase(revokeReferralRewardAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(revokeReferralRewardAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(revokeReferralRewardAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload;
        });
});
