import React, { useState } from 'react';
import { useAppDispatch } from '../../state/hooks';
import { verify2FAAction, verifyBackupCodeAction } from '../../state/auth/actions';
import Button from '../ui/Button';

interface TwoFactorVerificationProps {
  email: string;
  onSuccess: () => void;
}

export const TwoFactorVerification: React.FC<TwoFactorVerificationProps> = ({
  email,
  onSuccess
}) => {
  const dispatch = useAppDispatch();
  const [token, setToken] = useState('');
  const [isUsingBackupCode, setIsUsingBackupCode] = useState(false);
  const [backupCode, setBackupCode] = useState('');

  const handleTokenChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.replace(/\D/g, '').slice(0, 6);
    setToken(value);
  };

  const handleBackupCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.toUpperCase();
    setBackupCode(value);
  };

  const handleVerification = async () => {
    try {
      let result: any = undefined;
      if (isUsingBackupCode) {
        result = await dispatch(verifyBackupCodeAction({ email, code: backupCode })).unwrap();
      } else {
        result = await dispatch(verify2FAAction({ email, token })).unwrap();
      }
      if (onSuccess) onSuccess();
    } catch (e) {
      console.error(e);
    }
  };

  return (
    <div className="max-w-md mx-auto p-6 bg-white rounded-lg shadow-md">
      <h2 className="text-2xl font-bold mb-4">Two-Factor Authentication</h2>

      {!isUsingBackupCode ? (
        <>
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Enter the 6-digit code from your authenticator app:
            </label>
            <input
              type="text"
              value={token}
              onChange={handleTokenChange}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="000000"
              autoComplete="off"
            />
          </div>

          <Button
            onClick={handleVerification}
            disabled={token.length !== 6}
            className="w-full mb-4"
          >
            Verify
          </Button>

          <button
            onClick={() => setIsUsingBackupCode(true)}
            className="w-full text-sm text-blue-600 hover:text-blue-800"
          >
            Use backup code instead
          </button>
        </>
      ) : (
        <>
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Enter a backup code:
            </label>
            <input
              type="text"
              value={backupCode}
              onChange={handleBackupCodeChange}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Enter backup code"
              autoComplete="off"
            />
            <p className="mt-2 text-sm text-gray-500">
              Note: Each backup code can only be used once.
            </p>
          </div>

          <Button
            onClick={handleVerification}
            disabled={!backupCode}
            className="w-full mb-4"
          >
            Verify Backup Code
          </Button>

          <button
            onClick={() => setIsUsingBackupCode(false)}
            className="w-full text-sm text-blue-600 hover:text-blue-800"
          >
            Use authenticator app instead
          </button>
        </>
      )}
    </div>
  );
}; 