import { createReducer } from '@reduxjs/toolkit';
import {
    createCheckoutSessionAction,
    createPortalSessionAction,
    createPaypalPaymentAction,
    executePaypalPaymentAction,
    getSubscriptionAnalyticsAction,
    getRecentSubscriptionsAction
} from './actions';
import type { SubscriptionState } from './types';

const initialState: SubscriptionState = {
    isLoading: false,
    error: undefined,
};

export default createReducer(initialState, (builder) => {
    builder
        .addCase(createCheckoutSessionAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(createCheckoutSessionAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(createCheckoutSessionAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(createPortalSessionAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(createPortalSessionAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(createPortalSessionAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(createPaypalPaymentAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(createPaypalPaymentAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(createPaypalPaymentAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(executePaypalPaymentAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(executePaypalPaymentAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(executePaypalPaymentAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getSubscriptionAnalyticsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getSubscriptionAnalyticsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getSubscriptionAnalyticsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getRecentSubscriptionsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getRecentSubscriptionsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getRecentSubscriptionsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        });
});
