import { createReducer } from '@reduxjs/toolkit';
import {
    getMyTicketsAction,
    getAllTicketsAction,
    createTicketAction,
    respondToTicketAction,
    closeTicketAction
} from './actions';
import { SupportTicketState } from './types';

const initialState: SupportTicketState = {
    isLoading: false,
    error: undefined,
};

export default createReducer(initialState, (builder) => {
    builder
        .addCase(getMyTicketsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getMyTicketsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getMyTicketsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getAllTicketsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getAllTicketsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getAllTicketsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(createTicketAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(createTicketAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(createTicketAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(respondToTicketAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(respondToTicketAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(respondToTicketAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(closeTicketAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(closeTicketAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(closeTicketAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        });
}); 