import { jwtDecode } from "jwt-decode";

export const isTokenExpired = (token: string | null): boolean => {
    if (!token) {
        return true;
    }
    try {
        const { exp }: { exp?: number } = jwtDecode(token);
        if (!exp) {
            return true;
        }
        return Date.now() >= exp * 1000;
    } catch {
        return true;
    }
};