import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import { ApiError, ActionResponse } from '../global/types';
import {
    SupportTicketPaginatedResponse,
    SupportTicket,
    CreateTicketRequest
} from './types';

export const getMyTicketsService = async (
    queries?: { page?: string; limit?: string }
): Promise<ActionResponse<SupportTicketPaginatedResponse>> => {
    try {
        const response = await makeApiCall<SupportTicketPaginatedResponse>(
            '/api/support-tickets/my',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const getAllTicketsService = async (
    queries?: { page?: string; limit?: string }
): Promise<ActionResponse<SupportTicketPaginatedResponse>> => {
    try {
        const response = await makeApiCall<SupportTicketPaginatedResponse>(
            '/api/support-tickets',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const createTicketService = async (
    data: CreateTicketRequest
): Promise<ActionResponse<SupportTicket>> => {
    try {
        const response = await makeApiCall<SupportTicket>(
            '/api/support-tickets',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const respondToTicketService = async (
    id: string,
    data: { response: string }
): Promise<ActionResponse<{ ticket: SupportTicket }>> => {
    try {
        const response = await makeApiCall<{ ticket: SupportTicket }>(
            `/api/support-tickets/${id}/respond`,
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const closeTicketService = async (
    id: string
): Promise<ActionResponse<{ ticket: SupportTicket }>> => {
    try {
        const response = await makeApiCall<{ ticket: SupportTicket }>(
            `/api/support-tickets/${id}/close`,
            'POST'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
}; 