import { ChangeEvent, useCallback } from "react";

interface ToggleSwitchProps {
    checked: boolean;
    onChange: (e: ChangeEvent<HTMLInputElement>) => void;
    label: string;
    id: string;
}

const ToggleSwitch = ({
    checked,
    onChange,
    label,
    id
}: ToggleSwitchProps) => {
    const handleChange = useCallback(
        (e: ChangeEvent<HTMLInputElement>) => {
            onChange(e);
        },
        [onChange]
    );

    return (
        <label
            htmlFor={id}
            className="flex items-center gap-3 cursor-pointer select-none"
        >
            <span className="text-sm font-medium">
                {label}
            </span>
            <span className="relative">
                <input
                    id={id}
                    type="checkbox"
                    checked={checked}
                    onChange={handleChange}
                    className="sr-only peer"
                />
                <div
                    className="w-11 h-6 bg-gray-300 rounded-full peer-checked:bg-blue-600 transition-colors duration-200"
                />
                <div
                    className="absolute left-1 top-1 w-4 h-4 bg-white rounded-full shadow peer-checked:translate-x-5 transition-transform duration-200"
                />
            </span>
        </label>
    );
};

export default ToggleSwitch;