import React, {
    useEffect,
    useState,
    useCallback,
    FormEvent
} from 'react';
import { toast } from 'react-toastify';
import ToggleSwitch from '../components/ui/ToggleSwitch';
import NotificationModal from '../components/ui/notifications/NotificationModal';
import NotificationItem from '../components/ui/notifications/NotificationItem';
import { useAppDispatch } from '../state/hooks';
import {
    getNotificationPreferencesAction,
    updateNotificationPreferencesAction,
    getNotificationFeedAction,
    markNotificationReadAction,
    markAllNotificationsReadAction,
    deleteNotificationAction,
    clearAllNotificationsAction
} from '../state/notifications/actions';
import {
    Notification,
    NotificationPreference,
    NotificationFeedResponse
} from '../state/notifications/types';

const NotificationSettingsPage: React.FC = () => {
    const dispatch = useAppDispatch();

    const [settingsState, setSettingsState] = useState<NotificationPreference & {
        smsNumber: string;
        telegramChatId: string;
        discordWebhook: string;
    }>({
        email: true,
        sms: false,
        telegram: false,
        discord: false,
        smsNumber: '',
        telegramChatId: '',
        discordWebhook: '',
    });

    const setEmail = useCallback((value: boolean) => {
        setSettingsState((prev) => ({ ...prev, email: value }));
    }, []);
    const setSms = useCallback((value: boolean) => {
        setSettingsState((prev) => ({ ...prev, sms: value }));
    }, []);
    const setTelegram = useCallback((value: boolean) => {
        setSettingsState((prev) => ({ ...prev, telegram: value }));
    }, []);
    const setDiscord = useCallback((value: boolean) => {
        setSettingsState((prev) => ({ ...prev, discord: value }));
    }, []);
    const setSmsNumber = useCallback((value: string) => {
        setSettingsState((prev) => ({ ...prev, smsNumber: value }));
    }, []);
    const setTelegramChatId = useCallback((value: string) => {
        setSettingsState((prev) => ({ ...prev, telegramChatId: value }));
    }, []);
    const setDiscordWebhook = useCallback((value: string) => {
        setSettingsState((prev) => ({ ...prev, discordWebhook: value }));
    }, []);

    const [saving, setSaving] = useState<boolean>(false);
    const [prefLoading, setPrefLoading] = useState<boolean>(true);
    const [notifications, setNotifications] = useState<Notification[]>([]);
    const [feedLoading, setFeedLoading] = useState<boolean>(false);
    const [typeFilter, setTypeFilter] = useState<string>('all');
    const [unreadCount, setUnreadCount] = useState<number>(0);
    const [modalNotification, setModalNotification] = useState<Notification | null>(null);

    useEffect(() => {
        const fetchPreferences = async () => {
            setPrefLoading(true);
            try {
                const result = await dispatch(getNotificationPreferencesAction()).unwrap();
                setSettingsState({
                    email: result.email,
                    sms: result.sms,
                    telegram: result.telegram,
                    discord: result.discord,
                    smsNumber: result.smsNumber || '',
                    telegramChatId: result.telegramChatId || '',
                    discordWebhook: result.discordWebhook || '',
                });
            } catch (e) {
                console.error(e);
            } finally {
                setPrefLoading(false);
            }
        };
        fetchPreferences();
    }, [dispatch]);

    const fetchNotifications = useCallback(async () => {
        setFeedLoading(true);
        try {
            const result: NotificationFeedResponse = await dispatch(
                getNotificationFeedAction({ type: typeFilter })
            ).unwrap();
            setNotifications(result.notifications || []);
            setUnreadCount(
                (result.notifications || []).filter((n: Notification) => !n.read).length
            );
        } catch (e) {
            console.error(e);
        } finally {
            setFeedLoading(false);
        }
    }, [dispatch, typeFilter]);

    useEffect(() => {
        fetchNotifications();
    }, [fetchNotifications]);

    // useEffect(() => {
    //     const interval = setInterval(() => {
    //         fetchNotifications();
    //     }, 15000);
    //     return () => clearInterval(interval);
    // }, [fetchNotifications]);

    const handleMarkRead = useCallback(async (id: string) => {
        try {
            const result = await dispatch(markNotificationReadAction(id)).unwrap();
            if (result.success) {
                setNotifications((prev) =>
                    prev.map((n) => (n._id === id ? { ...n, read: true } : n))
                );
                setUnreadCount((prev) => Math.max(0, prev - 1));
                setModalNotification((prev) =>
                    prev && prev._id === id ? { ...prev, read: true } : prev
                );
            }
        } catch (e) {
            console.error(e);
        }
    }, [dispatch]);

    const handleMarkAllRead = useCallback(async () => {
        try {
            const result = await dispatch(markAllNotificationsReadAction()).unwrap();
            if (result.success) {
                setNotifications((prev) => prev.map((n) => ({ ...n, read: true })));
                setUnreadCount(0);
                setModalNotification((prev) => (prev ? { ...prev, read: true } : prev));
            }
        } catch (e) {
            console.error(e);
        }
    }, [dispatch]);

    const handleDelete = useCallback(async (id: string) => {
        try {
            const result = await dispatch(deleteNotificationAction(id)).unwrap();
            if (result.success) {
                setNotifications((prev) => prev.filter((n) => n._id !== id));
                setUnreadCount((prev) =>
                    Math.max(
                        0,
                        prev - (notifications.find((n) => n._id === id)?.read === false ? 1 : 0)
                    )
                );
                setModalNotification((prev) => (prev && prev._id === id ? null : prev));
            }
        } catch (e) {
            console.error(e);
        }
    }, [dispatch, notifications]);

    const handleClearAll = useCallback(async () => {
        try {
            const result = await dispatch(clearAllNotificationsAction()).unwrap();
            if (result.success) {
                setNotifications([]);
                setUnreadCount(0);
                setModalNotification(null);
            }
        } catch (e) {
            console.error(e);
        }
    }, [dispatch]);

    const validateFields = () => {
        if (settingsState.sms && !/^\+?\d{10,15}$/.test(settingsState.smsNumber)) {
            toast.error('Please enter a valid phone number (10-15 digits, optional +).', { autoClose: 2000 });
            return false;
        }
        if (settingsState.telegram && !/^(@[a-zA-Z0-9_]{5,32}|-?\d+)$/.test(settingsState.telegramChatId)) {
            toast.error('Please enter a valid Telegram Chat ID (number or @username).', { autoClose: 2000 });
            return false;
        }
        if (settingsState.discord && !/^https:\/\/(discord(app)?\.com)\/api\/webhooks\/\d+\/[\w-]+$/.test(settingsState.discordWebhook)) {
            toast.error('Please enter a valid Discord Webhook URL.', { autoClose: 2000 });
            return false;
        }
        return true;
    };

    const handleSave = useCallback(
        async (e: FormEvent<HTMLFormElement>) => {
            e.preventDefault();
            if (!validateFields()) return;
            setSaving(true);
            try {
                await dispatch(updateNotificationPreferencesAction({ ...settingsState })).unwrap();
            } catch (e) {
                console.error(e);
            } finally {
                setSaving(false);
            }
        },
        [settingsState, dispatch]
    );

    // Filter notifications by type
    const filteredNotifications = notifications.filter(
        (n) => typeFilter === 'all' || n.type === typeFilter
    );

    return (
        <div className="max-w-2xl mx-auto py-12 px-4">
            <h1 className="text-2xl font-bold mb-4 flex items-center gap-2">
                Notification Settings
                {unreadCount > 0 && (
                    <span
                        className="ml-2 bg-blue-600 text-white text-xs rounded-full px-2 py-0.5"
                        title="Unread notifications"
                    >
                        {unreadCount}
                    </span>
                )}
            </h1>
            <p className="text-accent mb-6">
                Configure your notification preferences for trade alerts, system messages, and more.
            </p>
            <form onSubmit={handleSave} className="space-y-6 mb-12">
                {prefLoading ? (
                    <div className="text-center text-accent py-8">
                        Loading preferences...
                    </div>
                ) : (
                    <>
                        <div>
                            <ToggleSwitch
                                id="email-toggle"
                                checked={settingsState.email}
                                onChange={(e) => setEmail(e.target.checked)}
                                label="Email Notifications"
                            />
                        </div>
                        <div>
                            <ToggleSwitch
                                id="sms-toggle"
                                checked={settingsState.sms}
                                onChange={(e) => setSms(e.target.checked)}
                                label="SMS Notifications"
                            />
                            {settingsState.sms && (
                                <input
                                    type="text"
                                    placeholder="Your phone number"
                                    value={settingsState.smsNumber}
                                    onChange={(e) => setSmsNumber(e.target.value)}
                                    className="mt-2 w-full px-3 py-2 border rounded-md"
                                />
                            )}
                        </div>
                        <div>
                            <ToggleSwitch
                                id="telegram-toggle"
                                checked={settingsState.telegram}
                                onChange={(e) => setTelegram(e.target.checked)}
                                label="Telegram Notifications"
                            />
                            {settingsState.telegram && (
                                <input
                                    type="text"
                                    placeholder="Your Telegram Chat ID"
                                    value={settingsState.telegramChatId}
                                    onChange={(e) => setTelegramChatId(e.target.value)}
                                    className="mt-2 w-full px-3 py-2 border rounded-md"
                                />
                            )}
                        </div>
                        <div>
                            <ToggleSwitch
                                id="discord-toggle"
                                checked={settingsState.discord}
                                onChange={(e) => setDiscord(e.target.checked)}
                                label="Discord Notifications"
                            />
                            {settingsState.discord && (
                                <input
                                    type="text"
                                    placeholder="Your Discord Webhook URL"
                                    value={settingsState.discordWebhook}
                                    onChange={(e) => setDiscordWebhook(e.target.value)}
                                    className="mt-2 w-full px-3 py-2 border rounded-md"
                                />
                            )}
                        </div>
                        <button
                            type="submit"
                            className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded-lg transition disabled:opacity-50"
                            disabled={saving}
                        >
                            {saving ? 'Saving...' : 'Save Preferences'}
                        </button>
                    </>
                )}
            </form>
            <div className="mb-8">
                <div className="flex items-center justify-between mb-4">
                    <h2 className="text-xl font-bold flex items-center gap-2">
                        Notification Feed
                        {unreadCount > 0 && (
                            <span
                                className="ml-2 bg-blue-600 text-white text-xs rounded-full px-2 py-0.5"
                                title="Unread notifications"
                            >
                                {unreadCount}
                            </span>
                        )}
                    </h2>
                    <div className="flex gap-2">
                        <button
                            className="text-blue-600 border border-blue-600 rounded px-3 py-1 text-sm hover:bg-blue-50"
                            onClick={fetchNotifications}
                            disabled={feedLoading}
                            title="Refresh notifications"
                        >
                            &#x21bb; Refresh
                        </button>
                        {unreadCount > 0 && (
                            <button
                                className="text-green-700 border border-green-700 rounded px-3 py-1 text-sm hover:bg-green-50"
                                onClick={handleMarkAllRead}
                                disabled={feedLoading}
                                title="Mark all as read"
                            >
                                Mark all as read
                            </button>
                        )}
                        {notifications.length > 0 && (
                            <button
                                className="text-red-700 border border-red-700 rounded px-3 py-1 text-sm hover:bg-red-50"
                                onClick={handleClearAll}
                                disabled={feedLoading}
                                title="Clear all notifications"
                            >
                                Clear all
                            </button>
                        )}
                    </div>
                </div>
                <div className="flex gap-4 mb-4 items-center">
                    <label className="text-sm text-accent">Type:</label>
                    <select
                        value={typeFilter}
                        onChange={(e) => setTypeFilter(e.target.value)}
                        className="border rounded px-2 py-1"
                    >
                        <option value="all">All</option>
                        <option value="error">Error</option>
                        <option value="alert">Alert</option>
                        <option value="info">Info</option>
                    </select>
                </div>
                {feedLoading ? (
                    <div className="text-center text-accent py-8">
                        Loading notifications...
                    </div>
                ) : filteredNotifications.length === 0 ? (
                    <div className="text-center text-accent py-8">
                        No notifications found.
                    </div>
                ) : (
                    <ul className="divide-y divide-border">
                        {filteredNotifications.map((notification, idx) => (
                            <NotificationItem
                                notification={notification}
                                key={notification._id || idx}
                                onRead={handleMarkRead}
                                onDelete={handleDelete}
                                onOpen={(n) => setModalNotification(n)}
                            />
                        ))}
                    </ul>
                )}
            </div>
            {modalNotification && (
                <NotificationModal
                    notification={modalNotification}
                    onClose={() => setModalNotification(null)}
                    onRead={handleMarkRead}
                    onDelete={handleDelete}
                />
            )}
        </div>
    );
};

export default NotificationSettingsPage;