import { createReducer } from '@reduxjs/toolkit';
import type { BotState } from './types';
import {
    backtestBotAction,
    backtestBotStrategyAction,
    createBotAction,
    deleteBotAction,
    getAllBotsAction,
    getBotLogsAction,
    getBotOpenPositionsAction,
    getBotPerformanceAction,
    getBotStatusAction,
    getPaperTradesAction,
    getPaperTradingStatsAction,
    getUserTradesAction,
    testBotRunAction,
    toggleBotAction,
    updateBotAction,
    updateBotApiKeyAction,
    updatePaperTradingConfigAction
} from './actions';

const initialState: BotState = {
    isLoading: false,
    error: undefined,
};

export default createReducer(initialState, (builder) => {
    builder
        .addCase(getAllBotsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getAllBotsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getAllBotsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(createBotAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(createBotAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(createBotAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(updateBotAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(updateBotAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(updateBotAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(deleteBotAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(deleteBotAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(deleteBotAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(toggleBotAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(toggleBotAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(toggleBotAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getBotLogsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getBotLogsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getBotLogsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getBotPerformanceAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getBotPerformanceAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getBotPerformanceAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(testBotRunAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(testBotRunAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(testBotRunAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(updatePaperTradingConfigAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(updatePaperTradingConfigAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(updatePaperTradingConfigAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getPaperTradesAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getPaperTradesAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getPaperTradesAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getPaperTradingStatsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getPaperTradingStatsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getPaperTradingStatsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(backtestBotAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(backtestBotAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(backtestBotAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(backtestBotStrategyAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(backtestBotStrategyAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(backtestBotStrategyAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(updateBotApiKeyAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(updateBotApiKeyAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(updateBotApiKeyAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getBotStatusAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getBotStatusAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getBotStatusAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getBotOpenPositionsAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getBotOpenPositionsAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getBotOpenPositionsAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getUserTradesAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getUserTradesAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getUserTradesAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        });
});