import { useState, useCallback, FormEvent } from 'react';
import { useAppDispatch } from '../../state/hooks';
import { validateEmail } from '../../utils/validation';
import { useForm } from '../../hooks/useForm';
import { forgotPasswordAction } from '../../state/auth/actions';

const ForgotPasswordPage = () => {
    const dispatch = useAppDispatch();
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const {
        formState,
        handleChange,
        validateForm
    } = useForm(['email'], { email: validateEmail });

    const handleSubmit = useCallback(
        async (event: FormEvent<HTMLFormElement>): Promise<void> => {
            event.preventDefault();
            if (isLoading) {
                return;
            }
            if (!validateForm()) {
                return;
            }
            try {
                setIsLoading(true);
                await dispatch(
                    forgotPasswordAction({
                        email: formState.email as string
                    })
                );
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, isLoading, formState.email, validateForm]
    );

    return (
        <div className="min-h-screen flex flex-col p-4">
            <div className="flex flex-grow items-center justify-center bg-background">
                <div
                    className="w-full max-w-md p-4 md:p-8 bg-white rounded-2xl shadow-strong border border-border flex flex-col items-center"
                >
                    <h2 className="text-2xl font-bold mb-4">Forgot Password</h2>
                    <form
                        className="w-full flex flex-col gap-4"
                        onSubmit={handleSubmit}
                    >
                        <div>
                            <input
                                type="email"
                                name="email"
                                placeholder="Email"
                                value={formState.email as string}
                                onChange={handleChange('email')}
                                className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black w-full"
                                autoComplete="email"
                                required
                            />
                            {formState.emailError && (
                                <div className="text-sm text-red-500 mt-1">
                                    {formState.emailErrorMessage}
                                </div>
                            )}
                        </div>
                        <button
                            type="submit"
                            className="w-full py-3 rounded-lg bg-black text-white font-semibold shadow-subtle hover:shadow-strong transition-all disabled:opacity-60"
                            disabled={isLoading}
                        >
                            {isLoading ? 'Sending...' : 'Send Reset Link'}
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default ForgotPasswordPage;