import { useState, useRef, useEffect, useCallback } from "react";
import { useNavigate, Link, Outlet } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "../../state/hooks";
import { RootState } from "../../state/store";
import { logoutAction } from "../../state/auth/actions";

type NavLink = {
    href: string;
    label: string;
    icon: string;
};

const navLinks: NavLink[] = [
    { href: "/dashboard", label: "Dashboard", icon: "" },
    { href: "/bots", label: "My Bots", icon: "" },
    { href: "/api-keys", label: "API Keys", icon: "" },
    { href: "/performance", label: "Performance", icon: "" },
    { href: "/backtesting", label: "Backtesting", icon: "" },
    { href: "/subscription", label: "Subscription", icon: "" },
    { href: "/referrals", label: "Referrals", icon: "" },
    { href: "/support", label: "Support", icon: "" },
    { href: "/profile", label: "Profile", icon: "" },
    { href: "/admin/users", label: "Users", icon: "" },
    { href: "/admin/tickets", label: "Tickets", icon: "" },
    { href: "/admin/revenue", label: "Revenue", icon: "" },
];

function getInitials(name: string): string {
    return name
        .split(' ')
        .map((n) => n[0])
        .slice(0, 2)
        .join('')
        .toUpperCase();
}

const Layout = () => {
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const user = useAppSelector((state: RootState) => state.user);

    const [isDropdownOpen, setIsDropdownOpen] = useState<boolean>(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState<boolean>(false);
    const dropdownRef = useRef<HTMLDivElement>(null);
    const mobileMenuRef = useRef<HTMLDivElement>(null);

    const handleLogout = useCallback(() => {
        dispatch(logoutAction());
    }, [dispatch]);

    const handleClick = useCallback((link: string) => () => {
        navigate(link);
        setIsDropdownOpen(false);
    }, [navigate]);

    const toggleDropdown = useCallback(() => {
        setIsDropdownOpen((prev) => !prev);
    }, []);

    const toggleMobileMenu = useCallback(() => {
        setIsMobileMenuOpen((prev) => !prev);
    }, []);

    useEffect(() => {
        if (isMobileMenuOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = '';
        }
        return () => {
            document.body.style.overflow = '';
        };
    }, [isMobileMenuOpen]);

    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (
                dropdownRef.current &&
                !dropdownRef.current.contains(event.target as Node)
            ) {
                setIsDropdownOpen(false);
            }
            if (
                mobileMenuRef.current &&
                !mobileMenuRef.current.contains(event.target as Node)
            ) {
                setIsMobileMenuOpen(false);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    return (
        <div className="min-h-screen flex bg-secondary w-full">
            <aside
                ref={mobileMenuRef}
                className={`fixed md:relative w-64 bg-white border-r border-border flex flex-col py-8 px-4 shadow-strong min-h-screen z-10 transition-transform duration-300 ease-in-out ${
                    isMobileMenuOpen ? 'translate-x-0' : '-translate-x-full md:translate-x-0'
                }`}
            >
                <div className="mb-8 flex items-center gap-2">
                    <span className="text-2xl font-bold text-black tracking-tight">
                        TradingBot
                    </span>
                </div>
                <nav className="flex-1 flex flex-col gap-2">
                    {navLinks.map((link) => (
                        <Link
                            key={link.href}
                            to={link.href}
                            className="flex items-center gap-3 px-4 py-2 rounded-lg text-black font-medium hover:bg-secondary hover:shadow-subtle transition-all"
                        >
                            <span className="text-lg">{link.icon}</span>
                            {link.label}
                        </Link>
                    ))}
                </nav>
                <div className="mt-8 flex flex-col items-center">
                    <div className="w-12 h-12 rounded-full bg-black text-white flex items-center justify-center text-xl font-bold mb-2">
                        {user?.avatar ? (
                            <img
                                src={user?.avatar}
                                alt=""
                                className="w-12 h-12 rounded-full"
                            />
                        ) : (
                            getInitials(user.name)
                        )}
                    </div>
                    
                    <div className="text-sm font-semibold text-black">
                        {user?.name || "User"}
                    </div>
                    <div className="text-xs text-accent">
                        {user?.email || "user@example.com"}
                    </div>
                </div>
            </aside>
            <div className="flex-1 flex flex-col min-h-screen w-full">
                <header className="h-16 bg-white border-b border-border flex items-center justify-between px-8 shadow-subtle">
                    <button
                        onClick={toggleMobileMenu}
                        className="md:hidden p-2 text-white hover:bg-white/10 rounded-lg transition-colors duration-200"
                    >
                        <svg
                            className="w-6 h-6 text-black"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                        >
                            {isMobileMenuOpen ? (
                                <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    strokeWidth={2}
                                    d="M6 18L18 6M6 6l12 12"
                                />
                            ) : (
                                <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    strokeWidth={2}
                                    d="M4 6h16M4 12h16M4 18h16"
                                />
                            )}
                        </svg>
                    </button>
                    <div className="flex-grow">
                        <div className="text-lg font-bold text-black">
                            Platform Dashboard
                        </div>
                    </div>
                    <div className="flex items-center gap-4">
                        <button className="relative p-2 rounded-full hover:bg-secondary transition-all">
                            <span className="sr-only">Notifications</span>
                            <svg
                                width="22"
                                height="22"
                                fill="none"
                                viewBox="0 0 24 24"
                            >
                                <path
                                    d="M12 22a2 2 0 0 0 2-2H10a2 2 0 0 0 2 2Zm6-6V11a6 6 0 1 0-12 0v5l-2 2v1h16v-1l-2-2Z"
                                    stroke="#18181b"
                                    strokeWidth="1.5"
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                />
                            </svg>
                            <span className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full"></span>
                        </button>
                        <div className="relative" ref={dropdownRef}>
                            <button
                                onClick={toggleDropdown}
                                className="flex items-center gap-2 px-3 py-2 rounded-lg bg-secondary text-black font-semibold hover:shadow-subtle transition-all"
                            >
                                {user?.avatar ? (
                                    <img
                                        src={user?.avatar}
                                        alt=""
                                        className="w-10 h-10 rounded-full"
                                    />
                                ) : (
                                    <span
                                        className="inline-flex items-center justify-center w-10 h-10 rounded-full bg-purple-200 text-purple-700 font-bold text-3xl mx-auto shadow cursor-pointer select-none"
                                    >
                                        {getInitials(user.name)}
                                    </span>
                                )}
                                <svg
                                    width="16"
                                    height="16"
                                    fill="none"
                                    viewBox="0 0 20 20"
                                    className={`transition-transform ${isDropdownOpen ? 'rotate-180' : ''}`}
                                >
                                    <path
                                        d="M6 8l4 4 4-4"
                                        stroke="#18181b"
                                        strokeWidth="1.5"
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                    />
                                </svg>
                            </button>
                            {isDropdownOpen && (
                                <div className="absolute right-0 mt-2 w-40 bg-white border border-border rounded-lg shadow-strong z-10">
                                    <button
                                        onClick={handleClick('/profile')}
                                        className="w-full text-left px-4 py-2 hover:bg-secondary rounded-t-lg"
                                    >
                                        Profile
                                    </button>
                                    <button
                                        onClick={handleClick('/settings')}
                                        className="w-full text-left px-4 py-2 hover:bg-secondary"
                                    >
                                        Settings
                                    </button>
                                    <button
                                        onClick={handleLogout}
                                        className="w-full text-left px-4 py-2 hover:bg-secondary rounded-b-lg text-red-600"
                                    >
                                        Logout
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>
                </header>
                <main className="flex-1 p-4 md:p-8 bg-secondary">
                    <div className="w-full h-full">
                        <Outlet />
                    </div>
                </main>
            </div>
        </div>
    );
};

export default Layout;