import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import type { ActionResponse, ApiError } from '../global/types';
import type { FileUploadResponse } from './types';

export const uploadFileService = async (
    file: File
): Promise<ActionResponse<FileUploadResponse>> => {
    try {
        const formData = new FormData();
        formData.append('file', file);
        const response = await makeApiCall<FileUploadResponse>(
            '/api/files/upload',
            'POST',
            {
                data: formData,
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            }
        );
        return handleActionResponse(response);
    } catch (error: unknown) {
        return handleActionError(error as ApiError);
    }
};