import { useEffect, useCallback } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAppDispatch } from '../../state/hooks';
import { setUserFromSocial } from '../../state/auth/actions';
import { UserTypes } from '../../state/auth/types';

interface DecodedData {
    token?: string;
    user: UserTypes;
}

const SocialCallback = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const dispatch = useAppDispatch();

    const handleCallback = useCallback(async () => {
        try {
            const params = new URLSearchParams(location.search);
            const data = params.get('data');

            if (!data) {
                throw new Error('No callback data received');
            }

            const decodedData: DecodedData = JSON.parse(
                decodeURIComponent(data)
            );
            const { token, user } = decodedData;

            await dispatch(
                setUserFromSocial({
                    id: user.id,
                    name: user.name || '',
                    email: user.email,
                    role: user.role,
                    avatar: user.avatar || '',
                    token: token || '',
                    subscriptionStatus: user.subscriptionStatus,
                    subscriptionPlan: user.subscriptionPlan,
                    hasPassword: user.hasPassword,
                    twoFAEnabled: !!user.twoFAEnabled,
                })
            );

            navigate('/dashboard');
        } catch (e) {
            // eslint-disable-next-line no-console
            console.error(e);
            navigate('/auth');
        }
    }, [location.search, dispatch, navigate]);

    useEffect(() => {
        handleCallback();
    }, [handleCallback]);

    return (
        <div className="min-h-screen flex w-full flex-grow items-center justify-center bg-gray-100">
            <div className="text-center">
                <h2 className="text-2xl font-semibold text-gray-800 mb-4">
                    Processing your login...
                </h2>
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto"></div>
            </div>
        </div>
    );
};

export default SocialCallback;