import React, { useCallback } from "react";
import { Notification } from "../../../state/notifications/types";

interface NotificationItemProps {
    notification: Notification;
    onRead: (id: string) => void;
    onDelete: (id: string) => void;
    onOpen: (n: Notification) => void;
}

const NotificationItem: React.FC<NotificationItemProps> = ({
    notification,
    onRead,
    onDelete,
    onOpen
}) => {
    const icon =
        notification.type === 'error' ? '' :
        notification.type === 'alert' ? '' :
        notification.type === 'info' ? '' :
        '';

    const color =
        notification.type === 'error' ? 'text-red-600' :
        notification.type === 'alert' ? 'text-yellow-500' :
        notification.type === 'info' ? 'text-blue-600' :
        'text-green-600';

    const isUnread: boolean = notification.read === false;

    const handleDelete = useCallback(
        (e: React.MouseEvent<HTMLButtonElement>) => {
            e.stopPropagation();
            onDelete(notification._id);
        },
        [onDelete, notification._id]
    );

    const handleOpen = useCallback(() => {
        onOpen(notification);
    }, [onOpen, notification]);

    return (
        <li
            className={`py-3 flex items-center gap-2 text-sm cursor-pointer ${
                isUnread ? 'bg-blue-50 font-semibold' : ''
            }`}
            onClick={handleOpen}
            title={isUnread ? 'Mark as read' : ''}
        >
            <span className={color}>{icon}</span>
            <span>{notification.message}</span>
            {notification.botName && (
                <span className="text-gray-400 ml-2">
                    ({notification.botName})
                </span>
            )}
            <span className="ml-auto text-xs text-gray-400">
                {new Date(notification.timestamp).toLocaleString()}
            </span>
            {isUnread && (
                <span
                    className="ml-2 inline-block w-2 h-2 rounded-full bg-blue-500"
                    title="Unread"
                />
            )}
            <button
                className="ml-4 text-gray-400 hover:text-red-600 p-1"
                title="Delete notification"
                onClick={handleDelete}
            >
                
            </button>
        </li>
    );
};

export default NotificationItem;