import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import {
    createCheckoutSessionService,
    createPortalSessionService,
    createPaypalPaymentService,
    executePaypalPaymentService,
    getSubscriptionAnalyticsService,
    getRecentSubscriptionsService
} from './services';
import type {
    CheckoutSessionResponse,
    ExecutePaypalPaymentRequest,
    PlanTypes,
    AnalyticsData,
    Subscription,
    RecentSubscriptionsResponse
} from './types';

export const createCheckoutSessionAction = createAsyncThunk<
    CheckoutSessionResponse,
    { priceId: string },
    { rejectValue: string }
>(
    'subscription/createCheckoutSession',
    async (data, { rejectWithValue }) => {
        try {
            const response = await createCheckoutSessionService(data);
            const result = handleActionResponse<CheckoutSessionResponse>(
                response,
                'Create checkout session successfully',
                'Failed to create checkout session'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error
                    ? error.message
                    : 'Failed to create checkout session';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

