import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import {
    getMyTicketsService,
    getAllTicketsService,
    createTicketService,
    respondToTicketService,
    closeTicketService
} from './services';
import type {
    SupportTicketPaginatedResponse,
    SupportTicket,
    CreateTicketRequest
} from './types';

export const getMyTicketsAction = createAsyncThunk<
    SupportTicketPaginatedResponse,
    { page?: string; limit?: string } | undefined,
    { rejectValue: string }
>(
    'supportTicket/getMyTickets',
    async (queries, { rejectWithValue }) => {
        try {
            const response = await getMyTicketsService(queries);
            const result = handleActionResponse<SupportTicketPaginatedResponse>(
                response,
                'Fetched my tickets successfully',
                'Failed to fetch my tickets'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to fetch my tickets';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

