export const createTicketAction = createAsyncThunk<
    SupportTicket,
    CreateTicketRequest,
    { rejectValue: string }
>(
    'supportTicket/createTicket',
    async (data, { rejectWithValue }) => {
        try {
            const response = await createTicketService(data);
            const result = handleActionResponse<SupportTicket>(
                response,
                'Ticket created successfully',
                'Failed to create ticket'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            toast.success('Ticket created successfully', { autoClose: 2000 });
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to create ticket';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

