import { useEffect, useCallback } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import { useAppDispatch } from './state/hooks';
import Layout from './components/layout/Layout';
import Home from './pages/Home';
import AuthPage from './pages/auth/Auth';
import ForgotPasswordPage from './pages/auth/ForgotPassword';
import ResetPasswordPage from './pages/auth/ResetPassword';
import VerifyEmailPage from './pages/auth/VerifyEmail';
import SocialCallback from './pages/auth/SocialCallback';
import DashboardOverview from './pages/Dashboard';
import ApiKeysPage from './pages/ApiKeys';
import BacktestingPage from './pages/Backtesting';
import BotsPage from './pages/Bots';
import PerformancePage from './pages/Performance';
import ProfilePage from './pages/Profile';
import ReferralsPage from './pages/Referrals';
import SubscriptionPage from './pages/Subscription';
import SupportPage from './pages/Support';
import NotificationSettingsPage from './pages/Notifications';
import StrategyBuilderPage from './pages/StrategyBuilder';
import PublicRoute from './components/auth/PublicRoute';
import PrivateRoute from './components/auth/PrivateRoute';
import { isTokenExpired } from './utils/isTokenExpired';
import { logoutAction } from './state/auth/actions';
import UsersManagementPage from './pages/admin/Users';
import TicketsManagementPage from './pages/admin/Tickets';
import RevenuePage from './pages/admin/Revenue';

const App: React.FC = () => {
    const dispatch = useAppDispatch();
    const { pathname } = useLocation();
    const token = localStorage.getItem('token');

    const handleLogout = useCallback((): void => {
        if (token && isTokenExpired(token)) {
            dispatch(logoutAction());
        }
    }, [token, dispatch]);

    useEffect(() => {
        handleLogout();
    }, [handleLogout]);

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return (
        <Routes>
            <Route element={<PublicRoute />}>
                <Route path="auth" element={<AuthPage />} />
                <Route path="forgot-password" element={<ForgotPasswordPage />} />
                <Route path="reset-password" element={<ResetPasswordPage />} />
                <Route path="verify-email" element={<VerifyEmailPage />} />
            </Route>
            <Route path="/social-callback" element={<SocialCallback />} />
            <Route element={<PrivateRoute />}>
                <Route element={<Layout />}>
                    <Route path="dashboard" element={<DashboardOverview />} />
                    <Route path="api-keys" element={<ApiKeysPage />} />
                    <Route path="backtesting" element={<BacktestingPage />} />
                    <Route path="bots" element={<BotsPage />} />
                    <Route path="performance" element={<PerformancePage />} />
                    <Route path="profile" element={<ProfilePage />} />
                    <Route path="referrals" element={<ReferralsPage />} />
                    <Route path="subscription" element={<SubscriptionPage />} />
                    <Route path="support" element={<SupportPage />} />
                    <Route path="settings" element={<NotificationSettingsPage />} />
                    <Route path="stratege-builder" element={<StrategyBuilderPage />} />
                    <Route path="admin/users" element={<UsersManagementPage />} />
                    <Route path="admin/tickets" element={<TicketsManagementPage />} />
                    <Route path="admin/revenue" element={<RevenuePage />} />
                </Route>
            </Route>
            <Route path="/" element={<Home />} />
        </Routes>
    );
};

export default App;