import {
    useState,
    useRef,
    useCallback,
    ChangeEvent,
    FormEvent
} from 'react';
import { useAppDispatch, useAppSelector } from '../state/hooks';
import { RootState } from '../state/store';
import Button from '../components/ui/Button';
import { updateProfileAction, changePasswordAction } from '../state/auth/actions';
import { TwoFactorSetup } from '../components/auth/TwoFactorSetup';

function getInitials(name: string): string {
    return name
        .split(' ')
        .map((n: string) => n[0])
        .slice(0, 2)
        .join('')
        .toUpperCase();
}

interface FormState {
    name: string;
    currentPassword: string;
    newPassword: string;
    confirmPassword: string;
    passwordErrorMessage: string;
}

const ProfilePage = () => {
    const dispatch = useAppDispatch();
    const user = useAppSelector((state: RootState) => state.user);

    const fileInputRef = useRef<HTMLInputElement | null>(null);
    const [profileSrc, setProfileSrc] = useState<string>(user?.avatar || '');
    const [profileImage, setProfileImage] = useState<File | undefined>(undefined);
    const [formState, setFormState] = useState<FormState>({
        name: user.name,
        currentPassword: '',
        newPassword: '',
        confirmPassword: '',
        passwordErrorMessage: ''
    });
    const [showCurrentPassword, setShowCurrentPassword] = useState<boolean>(false);
    const [showNewPassword, setShowNewPassword] = useState<boolean>(false);
    const [showConfirmPassword, setShowConfirmPassword] = useState<boolean>(false);
    const [showSetup, setShowSetup] = useState<boolean>(false);
    const [verificationToken, setVerificationToken] = useState<string>('');
    const [profileLoading, setProfileLoading] = useState<boolean>(false);
    const [passwordLoading, setPasswordLoading] = useState<boolean>(false);
    const [twoFALoading, setTwoFALoading] = useState<boolean>(false);

    const handleImageClick = useCallback((): void => {
        if (fileInputRef.current) {
            fileInputRef.current.click();
        }
    }, []);

    const handleFileChange = useCallback(
        (e: ChangeEvent<HTMLInputElement>): void => {
            const file = e.target.files?.[0];
            if (file) {
                setProfileSrc(URL.createObjectURL(file));
                setProfileImage(file);
            }
        },
        []
    );

    const handleChange = useCallback(
        (field: keyof FormState) => (event: ChangeEvent<HTMLInputElement>): void => {
            const value = event.target.value;
            setFormState(prev => ({
                ...prev,
                passwordErrorMessage: '',
                [field]: value,
            }));
        },
        []
    );

    const handleUpdateProfile = useCallback(
        async (e: FormEvent<HTMLFormElement>): Promise<void> => {
            e.preventDefault();
            setProfileLoading(true);
            try {
                const formData = new FormData();
                if (profileImage) {
                    formData.append('file', profileImage);
                }
                formData.append('name', formState.name as string);
                dispatch(updateProfileAction(formData));
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setProfileLoading(false);
            }
        },
        [formState, profileImage, dispatch]
    );

    const handlePasswordChange = useCallback(
        async (e: FormEvent<HTMLFormElement>): Promise<void> => {
            e.preventDefault();
            if (formState.newPassword !== formState.confirmPassword) {
                setFormState(prev => ({
                    ...prev,
                    passwordErrorMessage: 'Passwords do not match.',
                }));
                return;
            }
            setPasswordLoading(true);
            try {
                dispatch(changePasswordAction({
                    currentPassword: formState.currentPassword,
                    newPassword: formState.newPassword
                }));
                setFormState(prev => ({
                    ...prev,
                    currentPassword: '',
                    newPassword: '',
                    confirmPassword: ''
                }));
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setPasswordLoading(false);
            }
        },
        [formState, dispatch]
    );

    const handleDisable2FA = useCallback(async (): Promise<void> => {
        if (!verificationToken || verificationToken.length !== 6) {
            console.error('Enter a valid 6-digit code.');
            return;
        }
        setTwoFALoading(true);
        try {
            // await dispatch(disable2FA(verificationToken)).unwrap();
            // await dispatch(getMe());
            setVerificationToken('');
        } catch (e) {
            // eslint-disable-next-line no-console
            console.error(e);
        } finally {
            setTwoFALoading(false);
        }
    }, [verificationToken]);

    const toggleCurrentPasswordVisibility = useCallback((): void => {
        setShowCurrentPassword((prev) => !prev);
    }, []);

    const toggleNewPasswordVisibility = useCallback((): void => {
        setShowNewPassword((prev) => !prev);
    }, []);

    const toggleConfirmPasswordVisibility = useCallback((): void => {
        setShowConfirmPassword((prev) => !prev);
    }, []);

    return (
        <div className="max-w-4xl mx-auto py-6 sm:px-6 lg:px-8">
            <div className="bg-white shadow rounded-lg">
                <div className="px-4 py-5 sm:p-6">
                    <h2 className="text-lg font-medium text-gray-900 mb-4">
                        Profile Settings
                    </h2>
                    <form onSubmit={handleUpdateProfile} className="space-y-4">
                        <div className='flex justify-center'>
                            {profileSrc ? (
                                <img
                                    src={profileSrc}
                                    alt=''
                                    className='w-20 h-20 rounded-full'
                                />
                            ) : (
                                <span
                                    className="inline-flex items-center justify-center w-20 h-20 rounded-full bg-purple-200 text-purple-700 font-bold text-3xl mx-auto shadow cursor-pointer select-none"
                                    onClick={handleImageClick}
                                >
                                    {getInitials(user.name)}
                                </span>
                            )}
                            <input
                                type="file"
                                accept="image/*"
                                onChange={handleFileChange}
                                ref={fileInputRef}
                                style={{ display: 'none' }}
                            />
                        </div>
                        <div>
                            <label htmlFor="name" className="block text-sm font-medium text-gray-700">
                                Name
                            </label>
                            <input
                                type="text"
                                id="name"
                                placeholder='Name'
                                value={formState.name}
                                onChange={handleChange('name')}
                                className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm"
                                required
                            />
                        </div>
                        <div>
                            <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                                Email
                            </label>
                            <input
                                type="email"
                                id="email"
                                value={user?.email}
                                disabled
                                className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm cursor-not-allowed"
                            />
                        </div>
                        <button
                            type="submit"
                            className="px-6 py-2 rounded-lg bg-black text-white font-semibold"
                            disabled={profileLoading}
                        >
                            {profileLoading ? 'Saving...' : 'Save Changes'}
                        </button>
                    </form>

                    {/* Password Change Form */}
                    <div className="mt-10 pt-10 border-t border-gray-200">
                        <h3 className="text-lg font-medium text-gray-900">
                            Change Password
                        </h3>
                        <form onSubmit={handlePasswordChange} className="space-y-4 mt-6">
                            {user?.hasPassword && (
                                <div>
                                    <label htmlFor="currentPassword" className="block text-sm font-medium text-gray-700">
                                        Current Password
                                    </label>
                                    <div className='relative'>
                                        <input
                                            type={showCurrentPassword ? 'text' : 'password'}
                                            id="currentPassword"
                                            value={formState.currentPassword}
                                            onChange={handleChange('currentPassword')}
                                            className="mt-1 block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md shadow-sm"
                                        />
                                        <button
                                            type='button'
                                            onClick={toggleCurrentPasswordVisibility}
                                            className='absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer'
                                        >
                                            {showCurrentPassword ? (
                                                <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                    <path strokeLinecap='round' strokeLinejoin='round' d='M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88' />
                                                </svg>
                                            ) : (
                                                <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                    <path strokeLinecap='round' strokeLinejoin='round' d='M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z' />
                                                    <path strokeLinecap='round' strokeLinejoin='round' d='M15 12a3 3 0 11-6 0 3 3 0 016 0z' />
                                                </svg>
                                            )}
                                        </button>
                                    </div>
                                </div>
                            )}
                            <div>
                                <label htmlFor="newPassword" className="block text-sm font-medium text-gray-700">
                                    New Password
                                </label>
                                <div className='relative'>
                                    <input
                                        type={showNewPassword ? 'text' : 'password'}
                                        id="newPassword"
                                        value={formState.newPassword}
                                        onChange={handleChange('newPassword')}
                                        className="mt-1 block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md shadow-sm"
                                        required
                                    />
                                    <button
                                        type='button'
                                        onClick={toggleNewPasswordVisibility}
                                        className='absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer'
                                    >
                                        {showNewPassword ? (
                                            <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88' />
                                            </svg>
                                        ) : (
                                            <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z' />
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M15 12a3 3 0 11-6 0 3 3 0 016 0z' />
                                            </svg>
                                        )}
                                    </button>
                                </div>
                            </div>
                            <div>
                                <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                                    Confirm New Password
                                </label>
                                <div className='relative'>
                                    <input
                                        type={showConfirmPassword ? 'text' : 'password'}
                                        id="confirmPassword"
                                        value={formState.confirmPassword}
                                        onChange={handleChange('confirmPassword')}
                                        className="mt-1 block w-full px-3 py-2 pr-10 border border-gray-300 rounded-md shadow-sm"
                                        required
                                    />
                                    <button
                                        type='button'
                                        onClick={toggleConfirmPasswordVisibility}
                                        className='absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer'
                                    >
                                        {showConfirmPassword ? (
                                            <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88' />
                                            </svg>
                                        ) : (
                                            <svg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 24 24' strokeWidth={1.5} stroke='currentColor' className='w-5 h-5 text-gray-500'>
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z' />
                                                <path strokeLinecap='round' strokeLinejoin='round' d='M15 12a3 3 0 11-6 0 3 3 0 016 0z' />
                                            </svg>
                                        )}
                                    </button>
                                </div>
                                {formState.passwordErrorMessage && (
                                    <div className='text-sm text-red-500 mt-1'>
                                        {formState.passwordErrorMessage}
                                    </div>
                                )}
                            </div>
                            <button
                                type="submit"
                                className="px-6 py-2 rounded-lg bg-black text-white font-semibold"
                                disabled={passwordLoading}
                            >
                                {passwordLoading ? 'Changing...' : 'Change Password'}
                            </button>
                        </form>
                    </div>

                    {/* 2FA Section */}
                    <div className="mt-10 pt-10 border-t border-gray-200">
                        <h3 className="text-lg font-medium text-gray-900">
                            Two-Factor Authentication
                        </h3>
                        <div className="mt-3 max-w-xl text-sm text-gray-600">
                            <p>
                                Add an extra layer of security to your account by enabling two-factor
                                authentication.
                            </p>
                        </div>
                        {user?.twoFAEnabled ? (
                            <div className="mt-5">
                                <div className="rounded-md bg-green-50 p-4">
                                    <div className="flex">
                                        <div className="flex-shrink-0">
                                            <svg
                                                className="h-5 w-5 text-green-400"
                                                xmlns="http://www.w3.org/2000/svg"
                                                viewBox="0 0 20 20"
                                                fill="currentColor"
                                            >
                                                <path
                                                    fillRule="evenodd"
                                                    d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                                                    clipRule="evenodd"
                                                />
                                            </svg>
                                        </div>
                                        <div className="ml-3">
                                            <p className="text-sm font-medium text-green-800">
                                                Two-factor authentication is enabled
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="mt-5">
                                    <label
                                        htmlFor="verification"
                                        className="block text-sm font-medium text-gray-700"
                                    >
                                        Enter verification code to disable 2FA
                                    </label>
                                    <div className="mt-1 flex rounded-md shadow-sm">
                                        <input
                                            type="text"
                                            name="verification"
                                            id="verification"
                                            value={verificationToken}
                                            onChange={(e: ChangeEvent<HTMLInputElement>) =>
                                                setVerificationToken(
                                                    e.target.value.replace(/\D/g, '').slice(0, 6)
                                                )
                                            }
                                            className="flex-1 min-w-0 block w-full px-3 py-2 rounded-none rounded-l-md focus:ring-blue-500 focus:border-blue-500 sm:text-sm border-gray-300"
                                            placeholder="Enter 6-digit code"
                                        />
                                        <Button
                                            onClick={handleDisable2FA}
                                            disabled={verificationToken.length !== 6}
                                            className="-ml-px relative inline-flex items-center px-4 py-2 rounded-r-md"
                                        >
                                            Disable 2FA
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        ) : showSetup ? (
                            <div className="mt-5">
                                <TwoFactorSetup />
                                <button
                                    onClick={() => setShowSetup(false)}
                                    className="mt-4 text-sm text-gray-600 hover:text-gray-900"
                                >
                                    Cancel Setup
                                </button>
                            </div>
                        ) : (
                            <div className="mt-5">
                                <Button onClick={() => setShowSetup(true)}>
                                    Enable Two-Factor Authentication
                                </Button>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ProfilePage;