import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import { sendEmailService } from './services';
import type { SendEmailRequest } from './types';

export const sendEmailAction = createAsyncThunk<
    { success: boolean },
    SendEmailRequest,
    { rejectValue: string }
>(
    'email/send',
    async (
        data: SendEmailRequest,
        { rejectWithValue }
    ) => {
        try {
            const response = await sendEmailService(data);
            const result = handleActionResponse<{ success: boolean }>(
                response,
                'Send email successful',
                'Send email failed'
            );

            if (typeof result === 'string') {
                return rejectWithValue(result);
            }

            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Send email failed';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);
