import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import type { ApiError, ActionResponse } from '../global/types';
import type {
    ReferralRewardHistory,
    ReferralLeaderboardItem,
    ReferralStats,
    ListAllReferralRewardsResponse,
    ListAllReferralRewardsQueries,
    CreditReferralRewardRequest,
    RevokeReferralRewardRequest,
} from './types';

export const getReferralRewardHistoryService = async (): Promise<ActionResponse<{ history: ReferralRewardHistory[] }>> => {
    try {
        const response = await makeApiCall<{ history: ReferralRewardHistory[] }>(
            '/api/referrals/history',
            'GET'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const getReferralLeaderboardService = async (): Promise<ActionResponse<{ top: ReferralLeaderboardItem[] }>> => {
    try {
        const response = await makeApiCall<{ top: ReferralLeaderboardItem[] }>(
            '/api/referrals/leaderboard',
            'GET'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const getReferralStatsService = async (): Promise<ActionResponse<ReferralStats>> => {
    try {
        const response = await makeApiCall<ReferralStats>(
            '/api/referrals/stats',
            'GET'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const listAllReferralRewardsService = async (
    queries?: ListAllReferralRewardsQueries
): Promise<ActionResponse<ListAllReferralRewardsResponse>> => {
    try {
        const response = await makeApiCall<ListAllReferralRewardsResponse>(
            '/api/referrals/rewards',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const creditReferralRewardService = async (
    data: CreditReferralRewardRequest
): Promise<ActionResponse<{ success: boolean }>> => {
    try {
        const response = await makeApiCall<{ success: boolean }>(
            '/api/referrals/rewards/credit',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const revokeReferralRewardService = async (
    data: RevokeReferralRewardRequest
): Promise<ActionResponse<{ success: boolean }>> => {
    try {
        const response = await makeApiCall<{ success: boolean }>(
            '/api/referrals/rewards/revoke',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};
