import React, { useState, useCallback, useEffect, FormEvent } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { useAppDispatch } from "../../state/hooks";
import { signInAction, signUpAction } from "../../state/auth/actions";
import { TwoFactorVerification } from '../../components/auth/TwoFactorVerification';
import { validateEmail, validatePassword } from '../../utils/validation';
import { useForm } from '../../hooks/useForm';

const AuthPage = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const location = useLocation();

    const [isLogin, setIsLogin] = useState<boolean>(true);
    const [requires2FA, setRequires2FA] = useState<boolean>(false);
    const [showPassword, setShowPassword] = useState<boolean>(false);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const {
        formState,
        handleChange,
        validateForm,
        setFormState
    } = useForm(
        ['name', 'email', 'password', 'referralCode'],
        {
            email: validateEmail,
            password: validatePassword
        }
    );

    useEffect(() => {
        const params = new URLSearchParams(location.search);
        const ref = params.get('ref');
        if (ref) {
            setFormState((prev: Record<string, unknown>) => ({
                ...prev,
                referralCode: ref
            }));
        }
    }, [location.search, setFormState]);

    const togglePasswordVisibility = useCallback((): void => {
        setShowPassword((prev: boolean) => !prev);
    }, []);

    const handleGoogleLogin = useCallback((): void => {
        window.open('/api/auth/google', '_self');
    }, []);

    const handleSubmit = useCallback(
        async (event: FormEvent<HTMLFormElement>): Promise<void> => {
            event.preventDefault();
            if (isLoading) return;

            if (!validateForm()) {
                return;
            }

            try {
                setIsLoading(true);
                if (isLogin) {
                    const result = await dispatch(
                        signInAction({
                            email: formState.email as string,
                            password: formState.password as string
                        })
                    ).unwrap();

                    if ('requires2FA' in result) {
                        setRequires2FA(true);
                    } else {
                        navigate('/dashboard');
                    }
                } else {
                    await dispatch(
                        signUpAction({
                            name: formState.name as string,
                            email: formState.email as string,
                            password: formState.password as string,
                            referralCode:
                                (formState.referralCode as string) || undefined
                        })
                    );
                }
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [
            dispatch,
            isLoading,
            formState.name,
            formState.email,
            formState.password,
            formState.referralCode,
            isLogin,
            navigate,
            validateForm
        ]
    );

    const handle2FASuccess = useCallback((): void => {
        navigate('/dashboard');
    }, [navigate]);

    if (requires2FA) {
        return (
            <TwoFactorVerification
                email={formState.email as string}
                onSuccess={handle2FASuccess}
            />
        );
    }

    return (
        <div className="min-h-screen flex flex-col p-4">
            <div className="flex flex-grow items-center justify-center bg-gradient-to-br from-background to-secondary">
                <div className="w-full max-w-md p-4 md:p-8 bg-white rounded-2xl shadow-strong border border-border flex flex-col items-center">
                    <div className="flex w-full mb-8">
                        <button
                            className={`flex-1 py-2 rounded-l-xl font-semibold transition-all ${
                                isLogin
                                    ? "bg-black text-white shadow-subtle"
                                    : "bg-secondary text-black"
                            }`}
                            onClick={() => setIsLogin(true)}
                        >
                            Login
                        </button>
                        <button
                            className={`flex-1 py-2 rounded-r-xl font-semibold transition-all ${
                                !isLogin
                                    ? "bg-black text-white shadow-subtle"
                                    : "bg-secondary text-black"
                            }`}
                            onClick={() => setIsLogin(false)}
                        >
                            Register
                        </button>
                    </div>
                    <form
                        className="w-full flex flex-col gap-4"
                        onSubmit={handleSubmit}
                    >
                        {!isLogin && (
                            <>
                                <input
                                    type="text"
                                    name="name"
                                    placeholder="Name"
                                    value={formState.name as string}
                                    onChange={handleChange('name')}
                                    className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black"
                                    autoComplete="name"
                                    required
                                />
                                <input
                                    type="text"
                                    name="referralCode"
                                    placeholder="Referral Code (optional)"
                                    value={
                                        (formState.referralCode as string) || ''
                                    }
                                    onChange={handleChange('referralCode')}
                                    className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black"
                                    autoComplete="off"
                                />
                            </>
                        )}
                        <div>
                            <input
                                type="email"
                                name="email"
                                placeholder="Email"
                                value={formState.email as string}
                                onChange={handleChange('email')}
                                className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black w-full"
                                autoComplete="email"
                                required
                            />
                            {formState.emailError && (
                                <div className='text-sm text-red-500 mt-1'>
                                    {formState.emailErrorMessage}
                                </div>
                            )}
                        </div>
                        <div>
                            <div className='relative'>
                                <input
                                    type={showPassword ? 'text' : 'password'}
                                    name="password"
                                    placeholder="Password"
                                    value={formState.password as string}
                                    onChange={handleChange('password')}
                                    className="px-4 py-3 pr-10 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black w-full"
                                    autoComplete={isLogin ? "current-password" : "new-password"}
                                    required
                                />
                                <button
                                    type='button'
                                    onClick={togglePasswordVisibility}
                                    className='absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer'
                                >
                                    {showPassword ? (
                                        <svg
                                            xmlns='http://www.w3.org/2000/svg'
                                            fill='none'
                                            viewBox='0 0 24 24'
                                            strokeWidth={1.5}
                                            stroke='currentColor'
                                            className='w-5 h-5 text-gray-500'
                                        >
                                            <path
                                                strokeLinecap='round'
                                                strokeLinejoin='round'
                                                d='M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88'
                                            />
                                        </svg>
                                    ) : (
                                        <svg
                                            xmlns='http://www.w3.org/2000/svg'
                                            fill='none'
                                            viewBox='0 0 24 24'
                                            strokeWidth={1.5}
                                            stroke='currentColor'
                                            className='w-5 h-5 text-gray-500'
                                        >
                                            <path
                                                strokeLinecap='round'
                                                strokeLinejoin='round'
                                                d='M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z'
                                            />
                                            <path
                                                strokeLinecap='round'
                                                strokeLinejoin='round'
                                                d='M15 12a3 3 0 11-6 0 3 3 0 016 0z'
                                            />
                                        </svg>
                                    )}
                                </button>
                            </div>
                            {formState.passwordError && (
                                <div className='text-sm text-red-500 mt-1'>
                                    {formState.passwordErrorMessage}
                                </div>
                            )}
                        </div>
                        <button
                            type="submit"
                            className="w-full py-3 mt-2 rounded-lg bg-black text-white font-semibold shadow-subtle hover:shadow-strong transition-all disabled:opacity-60"
                            disabled={isLoading}
                        >
                            {isLoading
                                ? isLogin
                                    ? "Logging in..."
                                    : "Registering..."
                                : isLogin
                                    ? "Login"
                                    : "Register"
                            }
                        </button>
                        {isLogin && (
                            <a
                                href="/forgot-password"
                                className="mt-2 text-sm text-accent hover:underline text-center"
                            >
                                Forgot password?
                            </a>
                        )}
                    </form>
                    <div className="mt-6 w-full flex flex-col items-center">
                        <span className="text-xs text-accent mb-2">
                            or continue with
                        </span>
                        <button
                            onClick={handleGoogleLogin}
                            className="w-full py-3 rounded-lg border border-border bg-white text-black font-semibold shadow-subtle hover:shadow-strong flex items-center justify-center gap-2 transition-all"
                        >
                            <svg
                                width="20"
                                height="20"
                                fill="none"
                                viewBox="0 0 48 48"
                            >
                                <g>
                                    <path
                                        fill="#4285F4"
                                        d="M43.611 20.083H42V20H24v8h11.303C33.978 32.042 29.369 35 24 35c-6.075 0-11-4.925-11-11s4.925-11 11-11c2.507 0 4.81.857 6.646 2.278l6.366-6.366C33.527 6.671 28.977 5 24 5 12.954 5 4 13.954 4 25s8.954 20 20 20c10.493 0 19.5-8.507 19.5-19 0-1.27-.138-2.507-.389-3.694z"
                                    />
                                    <path
                                        fill="#34A853"
                                        d="M6.306 14.691l6.571 4.819C14.655 16.104 19.004 13 24 13c2.507 0 4.81.857 6.646 2.278l6.366-6.366C33.527 6.671 28.977 5 24 5c-6.627 0-12.31 2.69-16.694 7.691z"
                                    />
                                    <path
                                        fill="#FBBC05"
                                        d="M24 45c5.311 0 10.13-1.822 13.885-4.942l-6.418-5.263C29.37 36.042 26.791 37 24 37c-5.352 0-9.872-3.438-11.489-8.13l-6.57 5.066C7.67 41.02 15.27 45 24 45z"
                                    />
                                    <path
                                        fill="#EA4335"
                                        d="M43.611 20.083H42V20H24v8h11.303c-1.23 3.29-4.37 7-11.303 7-6.075 0-11-4.925-11-11s4.925-11 11-11c2.507 0 4.81.857 6.646 2.278l6.366-6.366C33.527 6.671 28.977 5 24 5c-6.627 0-12.31 2.69-16.694 7.691z"
                                    />
                                </g>
                            </svg>
                            Continue with Google
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AuthPage;