import { useState, useCallback, FormEvent } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useAppDispatch } from '../../state/hooks';
import { validatePassword } from '../../utils/validation';
import { useForm } from '../../hooks/useForm';
import { resetPasswordAction } from '../../state/auth/actions';

const ResetPasswordPage = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();

    const [showPassword, setShowPassword] = useState<boolean>(false);
    const [showRePassword, setShowRePassword] = useState<boolean>(false);
    const [isLoading, setIsLoading] = useState<boolean>(false);

    const {
        formState,
        handleChange,
        validateForm,
        setFormState
    } = useForm(['password', 'rePassword'], { password: validatePassword });

    const togglePasswordVisibility = useCallback((): void => {
        setShowPassword((prev) => !prev);
    }, []);

    const toggleRePasswordVisibility = useCallback((): void => {
        setShowRePassword((prev) => !prev);
    }, []);

    const handleSubmit = useCallback(
        async (event: FormEvent<HTMLFormElement>): Promise<void> => {
            event.preventDefault();
            if (isLoading) return;
            if (!validateForm()) {
                return;
            }

            if (formState.password !== formState.rePassword) {
                setFormState((prev: typeof formState) => ({
                    ...prev,
                    rePasswordError: true,
                    rePasswordErrorMessage: 'Passwords do not match.'
                }));
                return;
            }

            const token = searchParams.get('token');
            if (!token) {
                return;
            }

            try {
                setIsLoading(true);
                const result = await dispatch(
                    resetPasswordAction({
                        token,
                        password: formState.password as string
                    })
                );

                if (resetPasswordAction.fulfilled.match(result)) {
                    navigate('/auth');
                }
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, isLoading, formState, validateForm, navigate, setFormState, searchParams]
    );

    return (
        <div className="min-h-screen flex flex-col p-4">
            <div className="flex flex-grow items-center justify-center bg-background">
                <div className="w-full max-w-md p-4 md:p-8 bg-white rounded-2xl shadow-strong border border-border flex flex-col items-center">
                    <h2 className="text-2xl font-bold mb-4">Reset Password</h2>
                    <form className="w-full flex flex-col gap-4" onSubmit={handleSubmit}>
                        <div>
                            <div className="relative">
                                <input
                                    id="desktop-password"
                                    name="desktop-password"
                                    type={showPassword ? 'text' : 'password'}
                                    placeholder="New Password"
                                    autoComplete="current-password"
                                    value={formState.password as string}
                                    onChange={handleChange('password')}
                                    required
                                    className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black w-full"
                                />
                                <button
                                    type="button"
                                    onClick={togglePasswordVisibility}
                                    className="absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer"
                                >
                                    {showPassword ? (
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            fill="none"
                                            viewBox="0 0 24 24"
                                            strokeWidth={1.5}
                                            stroke="currentColor"
                                            className="w-5 h-5 text-gray-500"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88"
                                            />
                                        </svg>
                                    ) : (
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            fill="none"
                                            viewBox="0 0 24 24"
                                            strokeWidth={1.5}
                                            stroke="currentColor"
                                            className="w-5 h-5 text-gray-500"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z"
                                            />
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                                            />
                                        </svg>
                                    )}
                                </button>
                            </div>
                            {formState.passwordError && (
                                <div className="text-sm text-red-500 mt-1">
                                    {formState.passwordErrorMessage}
                                </div>
                            )}
                        </div>
                        <div>
                            <div className="relative">
                                <input
                                    id="desktop-repeat-password"
                                    name="desktop-repeat-password"
                                    type={showRePassword ? 'text' : 'password'}
                                    autoComplete="current-repeat-password"
                                    value={formState.rePassword as string}
                                    onChange={handleChange('rePassword')}
                                    placeholder="Repeat Password"
                                    required
                                    className="px-4 py-3 rounded-lg border border-border focus:outline-none focus:ring-2 focus:ring-black bg-secondary text-black w-full"
                                />
                                <button
                                    type="button"
                                    onClick={toggleRePasswordVisibility}
                                    className="absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer"
                                >
                                    {showRePassword ? (
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            fill="none"
                                            viewBox="0 0 24 24"
                                            strokeWidth={1.5}
                                            stroke="currentColor"
                                            className="w-5 h-5 text-gray-500"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88"
                                            />
                                        </svg>
                                    ) : (
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            fill="none"
                                            viewBox="0 0 24 24"
                                            strokeWidth={1.5}
                                            stroke="currentColor"
                                            className="w-5 h-5 text-gray-500"
                                        >
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z"
                                            />
                                            <path
                                                strokeLinecap="round"
                                                strokeLinejoin="round"
                                                d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                                            />
                                        </svg>
                                    )}
                                </button>
                            </div>
                            {formState.rePasswordError && (
                                <div className="text-sm text-red-500 mt-1">
                                    {formState.rePasswordErrorMessage}
                                </div>
                            )}
                        </div>
                        <button
                            type="submit"
                            className="w-full py-3 rounded-lg bg-black text-white font-semibold shadow-subtle hover:shadow-strong transition-all disabled:opacity-60"
                            disabled={isLoading}
                        >
                            {isLoading ? 'Resetting...' : 'Reset Password'}
                        </button>
                    </form>
                </div>
            </div>
        </div>
    );
};

export default ResetPasswordPage;