import React, { useState, useEffect, useCallback } from 'react';
import { useAppDispatch } from '../../../state/hooks';
import { BotTypes, BotLogTypes } from '../../../state/bot/types';
import LoadingSpinner from '../../../components/ui/LoadingSpinner';
import { getBotLogsAction } from '../../../state/bot/actions';

interface LogsModalProps {
    isOpen: boolean;
    onClose: () => void;
    bot: BotTypes;
}

const getLogIcon = (type: string): React.ReactElement => {
    switch (type.toLowerCase()) {
        case 'info':
            return (
                <svg className="w-4 h-4 text-blue-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                </svg>
            );
        case 'error':
            return (
                <svg className="w-4 h-4 text-red-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
            );
        case 'warning':
            return (
                <svg className="w-4 h-4 text-yellow-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                </svg>
            );
        case 'success':
            return (
                <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
            );
        default:
            return (
                <svg className="w-4 h-4 text-gray-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                </svg>
            );
    }
};

const getLogTypeColor = (type: string): string => {
    switch (type.toLowerCase()) {
        case 'info':
            return 'bg-blue-100 text-blue-800';
        case 'error':
            return 'bg-red-100 text-red-800';
        case 'warning':
            return 'bg-yellow-100 text-yellow-800';
        case 'success':
            return 'bg-green-100 text-green-800';
        default:
            return 'bg-gray-100 text-gray-800';
    }
};

const LogsModal = ({ isOpen, onClose, bot }: LogsModalProps): React.ReactElement | null => {
    const dispatch = useAppDispatch();
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [logs, setLogs] = useState<BotLogTypes[]>([]);
    const [totalLogsNum, setTotalLogsNum] = useState<number>(0);
    const [filterLogsNum, setFilterLogsNum] = useState<number>(0);
    const [page, setPage] = useState<number>(1);
    const [totalPages, setTotalPages] = useState<number>(1);
    const limit = 20;
    const [searchTerm, setSearchTerm] = useState<string>('');
    const [selectedType, setSelectedType] = useState<string>('all');

    const fetchAllLogs = useCallback(async (): Promise<void> => {
        try {
            setIsLoading(true);
            const result = await dispatch(
                getBotLogsAction({
                    id: bot._id,
                    page: String(page),
                    limit: String(limit),
                    search: searchTerm,
                    type: selectedType
                })
            ).unwrap();
            setLogs(result.logs);
            setTotalLogsNum(result.total);
            setFilterLogsNum(result.filterNumber);
            setTotalPages(result.totalPages);
        } catch (e) {
            setLogs([]);
            setTotalPages(1);
            // eslint-disable-next-line no-console
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    }, [dispatch, bot._id, searchTerm, selectedType, page]);

    useEffect(() => {
        setPage(1);
    }, [searchTerm, selectedType]);

    useEffect(() => {
        fetchAllLogs();
    }, [fetchAllLogs]);

    if (!isOpen) return null;

    return (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
            <div className="relative py-8 md:py-16 px-4 md:px-8">
                <div className="relative mx-auto p-5 border w-full max-w-4xl shadow-lg rounded-md bg-white">
                    <div className="mt-3">
                        {/* Header */}
                        <div className="flex items-center justify-between mb-6">
                            <div>
                                <h3 className="text-lg font-medium text-gray-900">
                                    Logs for {bot.name}
                                </h3>
                                <p className="text-sm text-gray-500 mt-1">
                                    {totalLogsNum} total logs  {filterLogsNum} filtered
                                </p>
                            </div>
                            <button
                                onClick={onClose}
                                className="text-gray-400 hover:text-gray-600"
                                type="button"
                                aria-label="Close logs modal"
                            >
                                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        {/* Filters */}
                        <div className="flex space-x-4 mb-4">
                            <div className="flex-1">
                                <label htmlFor="search" className="sr-only">Search logs</label>
                                <div className="relative">
                                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                                        <svg className="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                                        </svg>
                                    </div>
                                    <input
                                        type="text"
                                        id="search"
                                        value={searchTerm}
                                        onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearchTerm(e.target.value)}
                                        className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500 text-sm"
                                        placeholder="Search logs..."
                                    />
                                </div>
                            </div>
                            <div className="w-48">
                                <label htmlFor="type-filter" className="sr-only">Filter by type</label>
                                <select
                                    id="type-filter"
                                    value={selectedType}
                                    onChange={(e: React.ChangeEvent<HTMLSelectElement>) => setSelectedType(e.target.value)}
                                    className="block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 text-sm"
                                >
                                    <option value="all">All Types</option>
                                    <option value="info">Info</option>
                                    <option value="error">Error</option>
                                </select>
                            </div>
                        </div>

                        {/* Logs List */}
                        <div className="bg-gray-50 rounded-lg border border-gray-200 max-h-96 overflow-y-auto">
                            {isLoading ? (
                                <div className="flex items-center justify-center py-8">
                                    <LoadingSpinner size="md" />
                                    <span className="ml-2 text-gray-500">Loading logs...</span>
                                </div>
                            ) : logs.length === 0 ? (
                                <div className="text-center py-8">
                                    <svg className="mx-auto h-12 w-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                                    </svg>
                                    <h3 className="mt-2 text-sm font-medium text-gray-900">No logs found</h3>
                                    <p className="mt-1 text-sm text-gray-500">
                                        {logs.length === 0 ? 'No logs available for this bot.' : 'Try adjusting your search or filter criteria.'}
                                    </p>
                                </div>
                            ) : (
                                <div className="divide-y divide-gray-200">
                                    {logs.map((log: BotLogTypes) => (
                                        <div key={log._id} className="p-4 hover:bg-gray-100 transition-colors">
                                            <div className="flex items-start space-x-3">
                                                <div className="flex-shrink-0 mt-0.5">
                                                    {getLogIcon(log.type)}
                                                </div>
                                                <div className="flex-1 min-w-0">
                                                    <div className="flex items-center justify-between">
                                                        <div className="flex items-center space-x-2">
                                                            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getLogTypeColor(log.type)}`}>
                                                                {log.type}
                                                            </span>
                                                            <span className="text-xs text-gray-500 font-mono">
                                                                {new Date(log.timestamp).toLocaleString()}
                                                            </span>
                                                        </div>
                                                    </div>
                                                    <p className="mt-1 text-sm text-gray-900">{log.message}</p>
                                                    {log.data && (
                                                        <div className="mt-2">
                                                            <details className="group">
                                                                <summary className="cursor-pointer text-xs text-gray-500 hover:text-gray-700">
                                                                    View data ({Object.keys(log.data).length} fields)
                                                                </summary>
                                                                <pre className="mt-2 p-3 bg-gray-100 rounded text-xs overflow-x-auto">
                                                                    {JSON.stringify(log.data, null, 2)}
                                                                </pre>
                                                            </details>
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>

                        {/* Actions */}
                        <div className="flex items-center justify-between mt-6 md:space-x-3">
                            {/* Pagination Controls */}
                            {totalPages <= 1 ? (
                                <div />
                            ) : (
                                <div className="flex flex-grow justify-center items-center space-x-2">
                                    <button
                                        onClick={() => setPage((p: number) => Math.max(1, p - 1))}
                                        disabled={page === 1}
                                        className={`px-2 py-1 rounded border text-sm ${page === 1 ? 'bg-gray-200 text-gray-400 cursor-not-allowed' : 'bg-white text-gray-700 hover:bg-gray-50'}`}
                                        type="button"
                                    >
                                        Previous
                                    </button>
                                    <div className="bg-white text-gray-700 text-sm">
                                        {page} / {totalPages}
                                    </div>
                                    <button
                                        onClick={() => setPage((p: number) => Math.min(totalPages, p + 1))}
                                        disabled={page === totalPages}
                                        className={`px-2 py-1 rounded border text-sm ${page === totalPages ? 'bg-gray-200 text-gray-400 cursor-not-allowed' : 'bg-white text-gray-700 hover:bg-gray-50'}`}
                                        type="button"
                                    >
                                        Next
                                    </button>
                                </div>
                            )}
                            <button
                                onClick={onClose}
                                className="px-4 py-2 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                                type="button"
                            >
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LogsModal;