import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import {
    getAllBotsService,
    createBotService,
    updateBotService,
    deleteBotService,
    toggleBotService,
    getBotLogsService,
    getBotPerformanceService,
    testBotRunService,
    updatePaperTradingConfigService,
    getPaperTradesService,
    getPaperTradingStatsService,
    backtestBotService,
    backtestBotStrategyService,
    updateBotApiKeyService,
    getBotStatusService,
    getBotOpenPositionsService,
    getUserTradesService
} from './services';
import type {
    BotTypes,
    GetBotsResponse,
    GetBotLogsResponse,
    GetPaperTradesResponse,
    GetPaperTradingStatsResponse,
    PaperTradeConfig,
    BacktestBotRequest,
    BacktestBotResponse,
    BacktestBotStrategyRequest,
    UpdateBotApiKeyRequest,
    GetBotStatusResponse,
    GetBotOpenPositionsResponse,
    GetUserTradesResponse
} from './types';

// Get all bots
export const getAllBotsAction = createAsyncThunk<
    GetBotsResponse,
    { page?: string; limit?: string } | undefined,
    { rejectValue: string }
>(
    'bot/getAllBots',
    async (queries, { rejectWithValue }) => {
        try {
            const response = await getAllBotsService(queries);
            const result = handleActionResponse<GetBotsResponse>(
                response,
                'Fetched bots successfully',
                'Failed to fetch bots'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to fetch bots';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

