import { useEffect, useState, useCallback } from "react";
import LoadingSpinner from "../../components/ui/LoadingSpinner";
import { useAppDispatch } from "../../state/hooks";
import {
    getSubscriptionAnalyticsAction,
    getRecentSubscriptionsAction
} from "../../state/subscription/actions";
import type { AnalyticsData, Subscription } from "../../state/subscription/types";

const limit = 20;

export default function RevenuePage() {
    const dispatch = useAppDispatch();
    const [analytics, setAnalytics] = useState<AnalyticsData | null>(null);
    const [recentSubscriptions, setRecentSubscriptions] = useState<Subscription[]>([]);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [page, setPage] = useState<number>(1);
    const [totalPages, setTotalPages] = useState<number>(1);

    const fetchData = useCallback(async (): Promise<void> => {
        setIsLoading(true);
        try {
            const [analyticsRes, subscriptionsRes] = await Promise.all([
                dispatch(getSubscriptionAnalyticsAction()).unwrap(),
                dispatch(
                    getRecentSubscriptionsAction({
                        page: String(page),
                        limit: String(limit),
                    })
                ).unwrap(),
            ]);
            setAnalytics(analyticsRes);
            setRecentSubscriptions(subscriptionsRes.subscriptions);
            setTotalPages(subscriptionsRes.totalPages);
            setPage(subscriptionsRes.page);
        } catch (e) {
            // eslint-disable-next-line no-console
            console.error(e);
        }
        setIsLoading(false);
    }, [dispatch, page]);

    const handlePrevPage = useCallback((): void => {
        setPage((p) => Math.max(1, p - 1));
    }, []);

    const handleNextPage = useCallback((): void => {
        setPage((p) => Math.min(totalPages, p + 1));
    }, [totalPages]);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    if (isLoading) {
        return (
            <div className="flex justify-center items-center min-h-[200px]">
                <LoadingSpinner />
            </div>
        );
    }
    if (!analytics) return null;

    return (
        <div className="container mx-auto p-6">
            <h1 className="text-2xl font-bold mb-6">Revenue Analytics</h1>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
                <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-gray-500 text-sm">Total Users</h3>
                    <p className="text-2xl font-bold">{analytics.totalUsers}</p>
                </div>
                <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-gray-500 text-sm">Active Subscriptions</h3>
                    <p className="text-2xl font-bold">{analytics.subscriptions.active}</p>
                    <p className="text-sm text-gray-500">
                        {analytics.rates.active}% of total users
                    </p>
                </div>
                <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-gray-500 text-sm">Conversion Rate</h3>
                    <p className="text-2xl font-bold">{analytics.rates.conversion}%</p>
                    <p className="text-sm text-gray-500">Free to Paid</p>
                </div>
                <div className="bg-white p-4 rounded-lg shadow">
                    <h3 className="text-gray-500 text-sm">Total Revenue</h3>
                    <p className="text-2xl font-bold">
                        ${((analytics.plans.basic * 10) + (analytics.plans.premium * 25)).toLocaleString()}
                    </p>
                    <p className="text-sm text-gray-500">Monthly Recurring</p>
                </div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                <div className="bg-white p-6 rounded-lg shadow">
                    <h2 className="text-lg font-semibold mb-4">Plan Distribution</h2>
                    <div className="space-y-4">
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Premium Plan</span>
                                <span>{analytics.plans.premium} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-green-600 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.plans.premium / analytics.totalUsers) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Basic Plan</span>
                                <span>{analytics.plans.basic} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-blue-600 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.plans.basic / analytics.totalUsers) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Free Plan</span>
                                <span>{analytics.plans.free} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-gray-400 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.plans.free / analytics.totalUsers) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="bg-white p-6 rounded-lg shadow">
                    <h2 className="text-lg font-semibold mb-4">Subscription Status</h2>
                    <div className="space-y-4">
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Active</span>
                                <span>{analytics.subscriptions.active} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-green-600 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.subscriptions.active / analytics.subscriptions.total) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Trial</span>
                                <span>{analytics.subscriptions.trialing} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-yellow-500 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.subscriptions.trialing / analytics.subscriptions.total) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                        <div>
                            <div className="flex justify-between mb-1">
                                <span>Past Due</span>
                                <span>{analytics.subscriptions.pastDue} users</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                                <div
                                    className="bg-red-500 h-2 rounded-full"
                                    style={{
                                        width: `${(analytics.subscriptions.pastDue / analytics.subscriptions.total) * 100}%`,
                                    }}
                                />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="bg-white rounded-lg shadow pb-4">
                <div className="p-6 border-b">
                    <h2 className="text-lg font-semibold">Recent Subscriptions</h2>
                </div>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead className="bg-gray-50">
                            <tr>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    User
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Plan
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Status
                                </th>
                                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                                    Date
                                </th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {recentSubscriptions.map((sub: Subscription) => (
                                <tr key={sub._id}>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <div className="flex items-center">
                                            <div>
                                                <div className="text-sm font-medium text-gray-900">
                                                    {sub.name}
                                                </div>
                                                <div className="text-sm text-gray-500">
                                                    {sub.email}
                                                </div>
                                            </div>
                                        </div>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-blue-100 text-blue-800">
                                            {sub.subscriptionPlan}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <span
                                            className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                                                sub.subscriptionStatus === "active"
                                                    ? "bg-green-100 text-green-800"
                                                    : "bg-yellow-100 text-yellow-800"
                                            }`}
                                        >
                                            {sub.subscriptionStatus}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {new Date(sub.createdAt).toLocaleDateString()}
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
                {totalPages > 1 && (
                    <div className="flex flex-grow justify-center items-center space-x-2 mt-8">
                        <button
                            onClick={handlePrevPage}
                            disabled={page === 1}
                            className={`px-2 py-1 rounded border text-sm ${
                                page === 1
                                    ? "bg-gray-200 text-gray-400 cursor-not-allowed"
                                    : "bg-white text-gray-700 hover:bg-gray-50"
                            }`}
                        >
                            Previous
                        </button>
                        <div className="bg-white text-gray-700 text-sm">
                            {page} / {totalPages}
                        </div>
                        <button
                            onClick={handleNextPage}
                            disabled={page === totalPages}
                            className={`px-2 py-1 rounded border text-sm ${
                                page === totalPages
                                    ? "bg-gray-200 text-gray-400 cursor-not-allowed"
                                    : "bg-white text-gray-700 hover:bg-gray-50"
                            }`}
                        >
                            Next
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
}
