import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { 
    getApiKeysService,
    addApiKeyService,
    getApiKeyUsageService,
    deleteApiKeyService,
    testApiKeyService
} from './services';
import { handleActionResponse } from '../global/actions';
import type {
    ApiKeyListResponse,
    ApiKeyRequest,
    ApiKeyUsageResponse,
    ApiKeyTestResponse
} from './types';

export const getApiKeysAction = createAsyncThunk<
    ApiKeyListResponse,
    { page?: string; limit?: string } | undefined,
    { rejectValue: string }
>(
    'apiKeys/getApiKeys',
    async (queries, { rejectWithValue }) => {
        try {
            const response = await getApiKeysService(queries);
            const result = handleActionResponse<ApiKeyListResponse>(
                response,
                'Get api keys successfully',
                'Failed to get api keys'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage = error instanceof Error ? error.message : 'Failed to get api keys';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

