import { createReducer } from '@reduxjs/toolkit';
import {
    getNotificationPreferencesAction,
    updateNotificationPreferencesAction,
    getNotificationFeedAction,
    markNotificationReadAction,
    markAllNotificationsReadAction,
    deleteNotificationAction,
    clearAllNotificationsAction
} from './actions';
import type { NotificationState } from './types';

const initialState: NotificationState = {
    isLoading: false,
    error: undefined,
};

export default createReducer(initialState, (builder) => {
        builder
            .addCase(getNotificationPreferencesAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(getNotificationPreferencesAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(getNotificationPreferencesAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(updateNotificationPreferencesAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(updateNotificationPreferencesAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(updateNotificationPreferencesAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(getNotificationFeedAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(getNotificationFeedAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(getNotificationFeedAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(markNotificationReadAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(markNotificationReadAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(markNotificationReadAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(markAllNotificationsReadAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(markAllNotificationsReadAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(markAllNotificationsReadAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(deleteNotificationAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(deleteNotificationAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(deleteNotificationAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            })
            .addCase(clearAllNotificationsAction.pending, (state) => {
                state.isLoading = true;
                state.error = undefined;
            })
            .addCase(clearAllNotificationsAction.fulfilled, (state) => {
                state.isLoading = false;
            })
            .addCase(clearAllNotificationsAction.rejected, (state, action) => {
                state.isLoading = false;
                state.error = action.payload;
            });
    }
);
