import { createAsyncThunk } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';
import { handleActionResponse } from '../global/actions';
import {
    getAllUsersService,
    updateUserService,
    deleteUserService
} from './services';
import type {
    UpdateUserRequest,
    GetUsersResponse,
    UserTypes
} from './types';

export const getAllUsersAction = createAsyncThunk<
    GetUsersResponse,
    { page?: string; limit?: string } | undefined,
    { rejectValue: string }
>(
    'users/getAll',
    async (queries, { rejectWithValue }) => {
        try {
            const response = await getAllUsersService(queries);
            const result = handleActionResponse<GetUsersResponse>(
                response,
                'Get all users successfully',
                'Failed to get all users'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to get users';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

export const updateUserAction = createAsyncThunk<
    UserTypes,
    { id: string, data: UpdateUserRequest },
    { rejectValue: string }
>(
    'users/update',
    async ({ id, data }, { rejectWithValue }) => {
        try {
            const response = await updateUserService(id, data);
            const result = handleActionResponse<UserTypes>(
                response,
                'User updated successfully',
                'Failed to update user'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to update user';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

export const deleteUserAction = createAsyncThunk<
    { success: boolean },
    string,
    { rejectValue: string }
>(
    'users/delete',
    async (id, { rejectWithValue }) => {
        try {
            const response = await deleteUserService(id);
            const result = handleActionResponse<{ success: boolean }>(
                response,
                'User deleted successfully',
                'Failed to delete user'
            );
            if (typeof result === 'string') {
                return rejectWithValue(result);
            }
            return result;
        } catch (error: unknown) {
            const errorMessage =
                error instanceof Error ? error.message : 'Failed to delete user';
            toast.error(errorMessage, { autoClose: 2000 });
            return rejectWithValue(errorMessage);
        }
    }
);

