import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import type { ApiError, ActionResponse } from '../global/types';
import type {
    NotificationPreference,
    NotificationFeedResponse
} from './types';

export const getNotificationPreferencesService = async (): Promise<ActionResponse<NotificationPreference>> => {
    try {
        const response = await makeApiCall<NotificationPreference>(
            '/api/notifications/preferences',
            'GET'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const updateNotificationPreferencesService = async (
    data: NotificationPreference
): Promise<ActionResponse<NotificationPreference>> => {
    try {
        const response = await makeApiCall<NotificationPreference>(
            '/api/notifications/preferences',
            'PUT',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const getNotificationFeedService = async (
    queries?: { type?: string; page?: string; limit?: string }
): Promise<ActionResponse<NotificationFeedResponse>> => {
    try {
        const response = await makeApiCall<NotificationFeedResponse>(
            '/api/notifications/feed',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const markNotificationReadService = async (
    id: string
): Promise<ActionResponse<{ success: boolean }>> => {
    try {
        const response = await makeApiCall<{ success: boolean }>(
            `/api/notifications/read/${id}`,
            'POST'
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

