import { useEffect, useCallback } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { verifyEmailAction } from '../../state/auth/actions';
import { useAppDispatch } from '../../state/hooks';

const VerifyEmail = () => {
    const [searchParams] = useSearchParams();
    const navigate = useNavigate();
    const dispatch = useAppDispatch();

    const verify = useCallback(async () => {
        const token = searchParams.get('token');
        if (!token) {
            navigate('/auth');
            return;
        }
        try {
            const result = await dispatch(verifyEmailAction({ token }));
            if (verifyEmailAction.fulfilled.match(result)) {
                navigate('/');
            } else {
                navigate('/auth');
            }
        } catch (e) {
            // eslint-disable-next-line no-console
            console.error(e);
        }
    }, [dispatch, navigate, searchParams]);

    useEffect(() => {
        verify();
    }, [verify]);

    return (
        <div className="min-h-screen flex w-full flex-grow items-center justify-center bg-gray-100">
            <div className="text-center">
                <h2 className="text-2xl font-semibold text-gray-800 mb-4">
                    Verifying your email...
                </h2>
                <div
                    className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto"
                ></div>
            </div>
        </div>
    );
};

export default VerifyEmail; 