import {
    useEffect,
    useState,
    useCallback,
    FormEvent
} from "react";
import { useAppDispatch } from "../state/hooks";
import {
    getMyTicketsAction,
    createTicketAction
} from "../state/supportTicket/actions";
import { SupportTicket } from "../state/supportTicket/types";
import LoadingSpinner from "../components/ui/LoadingSpinner";

const SupportPage = () => {
    const dispatch = useAppDispatch();

    const [tickets, setTickets] = useState<SupportTicket[]>([]);
    const [subject, setSubject] = useState<string>("");
    const [message, setMessage] = useState<string>("");
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [isCreateLoading, setIsCreateLoading] = useState<boolean>(false);
    const [page, setPage] = useState<number>(1);
    const [totalPages, setTotalPages] = useState<number>(1);
    const limit: number = 20;

    const fetchTickets = useCallback(
        async (pageParam: number = page): Promise<void> => {
            try {
                setIsLoading(true);
                const result = await dispatch(
                    getMyTicketsAction({
                        page: String(pageParam),
                        limit: String(limit)
                    })
                ).unwrap();
                setTickets(result.tickets);
                setPage(result.page);
                setTotalPages(result.totalPages);
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, page]
    );

    useEffect(() => {
        fetchTickets(page);
    }, [fetchTickets, page]);

    const handleSubmit = useCallback(
        async (e: FormEvent<HTMLFormElement>): Promise<void> => {
            e.preventDefault();
            try {
                setIsCreateLoading(true);
                const result = await dispatch(
                    createTicketAction({
                        subject,
                        message
                    })
                );
                if (createTicketAction.fulfilled.match(result)) {
                    setSubject("");
                    setMessage("");
                    fetchTickets(1);
                }
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsCreateLoading(false);
            }
        },
        [dispatch, subject, message, fetchTickets]
    );

    const handlePrevPage = useCallback((): void => {
        setPage((p) => Math.max(1, p - 1));
    }, []);

    const handleNextPage = useCallback((): void => {
        setPage((p) => Math.min(totalPages, p + 1));
    }, [totalPages]);

    return (
        <div className="bg-white rounded-xl shadow-subtle p-8 border border-border max-w-3xl mx-auto mt-8">
            <h2 className="text-2xl font-bold mb-4">Support / Tickets</h2>
            <p className="text-accent mb-6">
                Submit and track support requests here.
            </p>

            <form onSubmit={handleSubmit} className="mb-8 space-y-4">
                <div>
                    <label className="block text-sm font-medium mb-1">Subject</label>
                    <input
                        type="text"
                        value={subject}
                        onChange={e => setSubject(e.target.value)}
                        className="w-full px-3 py-2 border rounded-md"
                        required
                    />
                </div>
                <div>
                    <label className="block text-sm font-medium mb-1">Message</label>
                    <textarea
                        value={message}
                        onChange={e => setMessage(e.target.value)}
                        className="w-full px-3 py-2 border rounded-md"
                        rows={4}
                        required
                    />
                </div>
                <button
                    type="submit"
                    className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded-lg transition disabled:opacity-50"
                    disabled={isCreateLoading}
                >
                    {isCreateLoading ? "Submitting..." : "Submit Ticket"}
                </button>
            </form>

            <div>
                <h3 className="text-lg font-semibold mb-2">Your Tickets</h3>
                {isLoading ? (
                    <div className="flex justify-center py-4">
                        <LoadingSpinner />
                    </div>
                ) : tickets.length === 0 ? (
                    <p className="text-accent">No tickets submitted yet.</p>
                ) : (
                    <div>
                        <div className="space-y-4 mb-8">
                            {tickets.map((ticket: SupportTicket) => (
                                <div key={ticket._id} className="border rounded-lg p-4">
                                    <div className="flex justify-between items-center gap-2 mb-2">
                                        <span className="font-semibold break-all">
                                            {ticket.subject}
                                        </span>
                                        <span
                                            className={`px-2 py-1 rounded text-xs ${
                                                ticket.status === "open"
                                                    ? "bg-green-100 text-green-800"
                                                    : ticket.status === "pending"
                                                    ? "bg-yellow-100 text-yellow-800"
                                                    : "bg-gray-100 text-gray-800"
                                            }`}
                                        >
                                            {ticket.status}
                                        </span>
                                    </div>
                                    <div className="text-sm text-gray-700 mb-2 break-all">
                                        {ticket.message}
                                    </div>
                                    <div className="text-xs text-gray-400 mb-1">
                                        Submitted: {new Date(ticket.createdAt).toLocaleString()}
                                    </div>
                                    {ticket.response && (
                                        <div className="bg-blue-50 border border-blue-200 rounded p-2 mt-2">
                                            <div className="text-xs text-blue-700 font-semibold mb-1">
                                                Admin Response:
                                            </div>
                                            <div className="text-sm text-blue-800 break-all">
                                                {ticket.response}
                                            </div>
                                        </div>
                                    )}
                                </div>
                            ))}
                        </div>
                        {totalPages > 1 && (
                            <div className="flex flex-grow justify-center items-center space-x-2">
                                <button
                                    onClick={handlePrevPage}
                                    disabled={page === 1}
                                    className={`px-2 py-1 rounded border text-sm ${
                                        page === 1
                                            ? "bg-gray-200 text-gray-400 cursor-not-allowed"
                                            : "bg-white text-gray-700 hover:bg-gray-50"
                                    }`}
                                >
                                    Previous
                                </button>
                                <div className="bg-white text-gray-700 text-sm">
                                    {page} / {totalPages}
                                </div>
                                <button
                                    onClick={handleNextPage}
                                    disabled={page === totalPages}
                                    className={`px-2 py-1 rounded border text-sm ${
                                        page === totalPages
                                            ? "bg-gray-200 text-gray-400 cursor-not-allowed"
                                            : "bg-white text-gray-700 hover:bg-gray-50"
                                    }`}
                                >
                                    Next
                                </button>
                            </div>
                        )}
                    </div>
                )}
            </div>
        </div>
    );
};

export default SupportPage;