import React, { useState, useCallback } from "react";

type IndicatorType = "SMA" | "EMA" | "RSI" | "MACD" | "BollingerBands";

interface IndicatorConfig {
    id: string;
    type: IndicatorType;
    params: Record<string, any>;
}

interface RuleConfig {
    id: string;
    condition: string;
    action: "buy" | "sell";
}

const StrategyBuilderPage = () => {
    const [strategyName, setStrategyName] = useState<string>("");
    const [indicators, setIndicators] = useState<IndicatorConfig[]>([]);
    const [rules, setRules] = useState<RuleConfig[]>([]);
    const [risk, setRisk] = useState<{ stopLoss: number; takeProfit: number; maxDailyLoss: number }>({
        stopLoss: 5,
        takeProfit: 10,
        maxDailyLoss: 500,
    });
    const [codeMode, setCodeMode] = useState<boolean>(false);
    const [customCode, setCustomCode] = useState<string>("// Write your custom strategy code here");

    const addIndicator = useCallback((type: IndicatorType): void => {
        const id = Math.random().toString(36).substr(2, 9);
        let params: Record<string, any> = {};
        switch (type) {
            case "SMA":
            case "EMA":
                params = { period: 14 };
                break;
            case "RSI":
                params = { period: 14, overbought: 70, oversold: 30 };
                break;
            case "MACD":
                params = { fast: 12, slow: 26, signal: 9 };
                break;
            case "BollingerBands":
                params = { period: 20, stdDev: 2 };
                break;
            default:
                break;
        }
        setIndicators((prev) => [...prev, { id, type, params }]);
    }, []);

    const removeIndicator = useCallback((id: string): void => {
        setIndicators((prev) => prev.filter((ind) => ind.id !== id));
    }, []);

    const addRule = useCallback((): void => {
        const id = Math.random().toString(36).substr(2, 9);
        setRules((prev) => [...prev, { id, condition: "", action: "buy" }]);
    }, []);

    const removeRule = useCallback((id: string): void => {
        setRules((prev) => prev.filter((rule) => rule.id !== id));
    }, []);

    const updateRuleCondition = useCallback((id: string, value: string): void => {
        setRules((prev) =>
            prev.map((r) => (r.id === id ? { ...r, condition: value } : r))
        );
    }, []);

    const updateRuleAction = useCallback((id: string, value: "buy" | "sell"): void => {
        setRules((prev) =>
            prev.map((r) => (r.id === id ? { ...r, action: value } : r))
        );
    }, []);

    const updateRisk = useCallback((key: keyof typeof risk, value: number): void => {
        setRisk((prev) => ({ ...prev, [key]: value }));
    }, []);

    const handleCustomCodeChange = useCallback((e: React.ChangeEvent<HTMLTextAreaElement>): void => {
        setCustomCode(e.target.value);
    }, []);

    const handleStrategyNameChange = useCallback((e: React.ChangeEvent<HTMLInputElement>): void => {
        setStrategyName(e.target.value);
    }, []);

    const handleCodeModeChange = useCallback((e: React.ChangeEvent<HTMLInputElement>): void => {
        setCodeMode(e.target.checked);
    }, []);

    return (
        <div className="max-w-4xl mx-auto py-12 px-4">
            <h1 className="text-2xl font-bold mb-4">Custom Strategy Builder</h1>
            <p className="text-accent mb-8">
                Create and customize your own trading strategies using indicators, risk management, and custom logic.
            </p>

            <div className="mb-6">
                <label className="block text-sm font-medium mb-1">Strategy Name</label>
                <input
                    type="text"
                    value={strategyName}
                    onChange={handleStrategyNameChange}
                    className="w-full px-3 py-2 border rounded-md"
                    placeholder="My Awesome Strategy"
                />
            </div>

            <div className="mb-8">
                <h2 className="text-lg font-semibold mb-2">Indicators</h2>
                <div className="flex gap-2 mb-4">
                    {["SMA", "EMA", "RSI", "MACD", "BollingerBands"].map((type) => (
                        <button
                            key={type}
                            className="bg-gray-200 hover:bg-gray-300 text-sm px-3 py-1 rounded"
                            onClick={() => addIndicator(type as IndicatorType)}
                            type="button"
                        >
                            + {type}
                        </button>
                    ))}
                </div>
                {indicators.length === 0 ? (
                    <p className="text-accent">No indicators added yet.</p>
                ) : (
                    <ul className="space-y-2">
                        {indicators.map((ind) => (
                            <li key={ind.id} className="flex items-center gap-2">
                                <span className="font-mono">{ind.type}</span>
                                <span className="text-xs text-gray-500">
                                    {Object.entries(ind.params)
                                        .map(([k, v]) => `${k}: ${v}`)
                                        .join(", ")}
                                </span>
                                <button
                                    className="text-red-500 hover:underline text-xs"
                                    onClick={() => removeIndicator(ind.id)}
                                    type="button"
                                >
                                    Remove
                                </button>
                            </li>
                        ))}
                    </ul>
                )}
            </div>

            <div className="mb-8">
                <h2 className="text-lg font-semibold mb-2">Entry/Exit Rules</h2>
                <button
                    className="bg-gray-200 hover:bg-gray-300 text-sm px-3 py-1 rounded mb-2"
                    onClick={addRule}
                    type="button"
                >
                    + Add Rule
                </button>
                {rules.length === 0 ? (
                    <p className="text-accent">No rules defined yet.</p>
                ) : (
                    <ul className="space-y-2">
                        {rules.map((rule) => (
                            <li key={rule.id} className="flex items-center gap-2">
                                <input
                                    type="text"
                                    value={rule.condition}
                                    onChange={(e) => updateRuleCondition(rule.id, e.target.value)}
                                    className="px-2 py-1 border rounded text-xs w-64"
                                    placeholder="e.g. SMA(14) > SMA(50)"
                                />
                                <select
                                    value={rule.action}
                                    onChange={(e) => updateRuleAction(rule.id, e.target.value as "buy" | "sell")}
                                    className="px-2 py-1 border rounded text-xs"
                                >
                                    <option value="buy">Buy</option>
                                    <option value="sell">Sell</option>
                                </select>
                                <button
                                    className="text-red-500 hover:underline text-xs"
                                    onClick={() => removeRule(rule.id)}
                                    type="button"
                                >
                                    Remove
                                </button>
                            </li>
                        ))}
                    </ul>
                )}
            </div>

            <div className="mb-8">
                <h2 className="text-lg font-semibold mb-2">Risk Management</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                    <div>
                        <label className="block text-xs font-medium mb-1">Stop Loss (%)</label>
                        <input
                            type="number"
                            value={risk.stopLoss}
                            onChange={(e) => updateRisk("stopLoss", parseFloat(e.target.value))}
                            className="w-full px-2 py-1 border rounded"
                            min={0}
                        />
                    </div>
                    <div>
                        <label className="block text-xs font-medium mb-1">Take Profit (%)</label>
                        <input
                            type="number"
                            value={risk.takeProfit}
                            onChange={(e) => updateRisk("takeProfit", parseFloat(e.target.value))}
                            className="w-full px-2 py-1 border rounded"
                            min={0}
                        />
                    </div>
                    <div>
                        <label className="block text-xs font-medium mb-1">Max Daily Loss ($)</label>
                        <input
                            type="number"
                            value={risk.maxDailyLoss}
                            onChange={(e) => updateRisk("maxDailyLoss", parseFloat(e.target.value))}
                            className="w-full px-2 py-1 border rounded"
                            min={0}
                        />
                    </div>
                </div>
            </div>

            <div className="mb-8">
                <label className="flex items-center gap-2 mb-2">
                    <input
                        type="checkbox"
                        checked={codeMode}
                        onChange={handleCodeModeChange}
                    />
                    <span>Switch to Code Editor (Advanced)</span>
                </label>
                {codeMode && (
                    <textarea
                        value={customCode}
                        onChange={handleCustomCodeChange}
                        className="w-full h-40 border rounded px-3 py-2 font-mono"
                    />
                )}
            </div>

            <button
                className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded-lg transition disabled:opacity-50"
                type="button"
                onClick={() => alert("Strategy saving coming soon!")}
            >
                Save Strategy
            </button>
        </div>
    );
};

export default StrategyBuilderPage;