import { createReducer } from '@reduxjs/toolkit';
import {
    getApiKeysAction,
    addApiKeyAction,
    getApiKeyUsageAction,
    deleteApiKeyAction,
    testApiKeyAction
} from './actions';
import type { ApiKeyState } from './types';

const initialState: ApiKeyState = {
    isLoading: false,
    error: undefined,
    supportedExchanges: [
        { value: 'binance', label: 'Binance' } as { value: string; label: string },
        { value: 'coinbase', label: 'Coinbase Advanced Trade' } as { value: string; label: string },
        { value: 'kraken', label: 'Kraken' } as { value: string; label: string },
    ],
};

const apiKeysReducer = createReducer(initialState, (builder) => {
    builder
        .addCase(getApiKeysAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getApiKeysAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getApiKeysAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(addApiKeyAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(addApiKeyAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(addApiKeyAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(getApiKeyUsageAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(getApiKeyUsageAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(getApiKeyUsageAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(deleteApiKeyAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(deleteApiKeyAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(deleteApiKeyAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        })
        .addCase(testApiKeyAction.pending, (state) => {
            state.isLoading = true;
            state.error = undefined;
        })
        .addCase(testApiKeyAction.fulfilled, (state) => {
            state.isLoading = false;
        })
        .addCase(testApiKeyAction.rejected, (state, action) => {
            state.isLoading = false;
            state.error = action.payload as string;
        });
});

export default apiKeysReducer; 