import { useEffect, useState, useCallback } from 'react';
import { useAppDispatch } from '../../state/hooks';
import {
    getAllTicketsAction,
    respondToTicketAction,
    closeTicketAction
} from '../../state/supportTicket/actions';
import { SupportTicket } from '../../state/supportTicket/types';

interface ResponseTextMap {
    [id: string]: string;
}

interface ActionLoadingMap {
    [id: string]: boolean;
}

interface TicketResponse {
    response: string;
}

const limit: number = 20;

const statusClasses: Record<string, string> = {
    open: 'bg-green-100 text-green-800',
    pending: 'bg-yellow-100 text-yellow-800',
    default: 'bg-gray-100 text-gray-800',
};

const renderTicketStatus = (status: string) => {
    return (
        <span
            className={`px-2 py-1 rounded text-xs ${
                statusClasses[status as keyof typeof statusClasses] || statusClasses.default
            }`}
        >
            {status}
        </span>
    );
};

const renderTicketResponse = (ticket: SupportTicket) => {
    if (!ticket.response) return null;
    return (
        <div className="bg-blue-50 border border-blue-200 rounded p-2 mt-2">
            <div className="text-xs text-blue-700 font-semibold mb-1">
                Admin Response:
            </div>
            <div className="text-sm text-blue-800">{ticket.response}</div>
        </div>
    );
};

export default function TicketsManagementPage() {
    const dispatch = useAppDispatch();

    const [tickets, setTickets] = useState<SupportTicket[]>([]);
    const [loading, setLoading] = useState<boolean>(false);
    const [error, setError] = useState<string | null>(null);
    const [responseText, setResponseText] = useState<ResponseTextMap>({});
    const [actionLoading, setActionLoading] = useState<ActionLoadingMap>({});
    const [page, setPage] = useState<number>(1);
    const [totalPages, setTotalPages] = useState<number>(1);

    const fetchTickets = useCallback(async (): Promise<void> => {
        try {
            setLoading(true);
            const result = await dispatch(
                getAllTicketsAction({
                    page: String(page),
                    limit: String(limit),
                })
            ).unwrap();
            setTickets(result.tickets);
            setTotalPages(result.totalPages);
            setPage(result.page);
        } catch (e) {
            console.error(e);
            setError('Failed to fetch tickets.');
        } finally {
            setLoading(false);
        }
    }, [dispatch, page]);

    const handleResponseTextChange = useCallback((ticketId: string, value: string): void => {
        setResponseText((prev) => ({ ...prev, [ticketId]: value }));
    }, []);

    const handleRespond = useCallback(
        async (ticketId: string): Promise<void> => {
            try {
                setActionLoading((prev) => ({ ...prev, [ticketId]: true }));
                const ticketResponse: TicketResponse = { response: responseText[ticketId] };
                const result = await dispatch(
                    respondToTicketAction({
                        id: ticketId,
                        data: ticketResponse,
                    })
                );
                if (respondToTicketAction.fulfilled.match(result)) {
                    setResponseText((prev) => ({ ...prev, [ticketId]: '' }));
                    await fetchTickets();
                }
            } catch (e) {
                console.error(e);
            } finally {
                setActionLoading((prev) => ({ ...prev, [ticketId]: false }));
            }
        },
        [dispatch, responseText, fetchTickets]
    );

    const handleClose = useCallback(
        async (ticketId: string): Promise<void> => {
            try {
                setActionLoading((prev) => ({ ...prev, [ticketId]: true }));
                const result = await dispatch(closeTicketAction(ticketId));
                if (closeTicketAction.fulfilled.match(result)) {
                    await fetchTickets();
                }
            } catch (e) {
                console.error(e);
            } finally {
                setActionLoading((prev) => ({ ...prev, [ticketId]: false }));
            }
        },
        [dispatch, fetchTickets]
    );

    const handlePrevPage = useCallback((): void => {
        setPage((p) => Math.max(1, p - 1));
    }, []);

    const handleNextPage = useCallback((): void => {
        setPage((p) => Math.min(totalPages, p + 1));
    }, [totalPages]);

    useEffect(() => {
        fetchTickets();
    }, [fetchTickets]);

    const renderTicketActions = useCallback((ticket: SupportTicket) => {
        if (ticket.status === 'closed') return null;
        return (
            <div className="mt-4 flex flex-col md:flex-row gap-2">
                <textarea
                    className="w-full px-3 py-2 border rounded-md"
                    rows={2}
                    placeholder="Write a response..."
                    value={responseText[ticket._id] || ''}
                    onChange={(e) => handleResponseTextChange(ticket._id, e.target.value)}
                />
                <button
                    className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-4 py-2 rounded-lg transition disabled:opacity-50"
                    disabled={actionLoading[ticket._id] || !responseText[ticket._id]}
                    onClick={() => handleRespond(ticket._id)}
                >
                    {actionLoading[ticket._id] ? 'Sending...' : 'Send Response'}
                </button>
                <button
                    className="bg-gray-500 hover:bg-gray-600 text-white font-semibold px-4 py-2 rounded-lg transition disabled:opacity-50"
                    disabled={actionLoading[ticket._id]}
                    onClick={() => handleClose(ticket._id)}
                >
                    {actionLoading[ticket._id] ? 'Closing...' : 'Close Ticket'}
                </button>
            </div>
        );
    }, [actionLoading, responseText, handleRespond, handleClose, handleResponseTextChange]);

    return (
        <div className="max-w-4xl mx-auto py-12 px-4">
            <h1 className="text-2xl font-bold mb-4">
                Admin: Support Ticket Management
            </h1>
            <p className="text-accent mb-6">
                View and manage all user support tickets.
            </p>
            {error && <div className="text-red-600 mb-4">{error}</div>}
            {loading ? (
                <div>Loading tickets...</div>
            ) : tickets.length === 0 ? (
                <p className="text-accent">No support tickets found.</p>
            ) : (
                <>
                    <div className="space-y-6">
                        {tickets.map((ticket: SupportTicket) => (
                            <div key={ticket._id} className="border rounded-lg p-4">
                                <div className="flex justify-between items-center mb-2">
                                    <span className="font-semibold">{ticket.subject}</span>
                                    {renderTicketStatus(ticket.status)}
                                </div>
                                <div className="text-sm text-gray-700 mb-2">
                                    {ticket.message}
                                </div>
                                <div className="text-xs text-gray-400 mb-1">
                                    Submitted: {new Date(ticket.createdAt).toLocaleString()} by{' '}
                                    {typeof ticket.user === 'object' && ticket.user && 'email' in ticket.user
                                        ? ticket.user.email
                                        : 'Unknown'}
                                </div>
                                {renderTicketResponse(ticket)}
                                {renderTicketActions(ticket)}
                            </div>
                        ))}
                    </div>
                    {totalPages > 1 && (
                        <div className="flex flex-grow justify-center items-center space-x-2 mt-8">
                            <button
                                onClick={handlePrevPage}
                                disabled={page === 1}
                                className={`px-2 py-1 rounded border text-sm ${
                                    page === 1
                                        ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                                        : 'bg-white text-gray-700 hover:bg-gray-50'
                                }`}
                            >
                                Previous
                            </button>
                            <div className="bg-white text-gray-700 text-sm">
                                {page} / {totalPages}
                            </div>
                            <button
                                onClick={handleNextPage}
                                disabled={page === totalPages}
                                className={`px-2 py-1 rounded border text-sm ${
                                    page === totalPages
                                        ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                                        : 'bg-white text-gray-700 hover:bg-gray-50'
                                }`}
                            >
                                Next
                            </button>
                        </div>
                    )}
                </>
            )}
        </div>
    );
}
