import {
    useEffect,
    useState,
    useCallback,
    FormEvent,
} from 'react';
import { Link } from 'react-router-dom';
import { useAppDispatch } from '../state/hooks';
import { ApiKey, ApiKeyUsageResponse } from '../state/apiKey/types';
import {
    getApiKeysAction,
    addApiKeyAction,
    getApiKeyUsageAction,
    deleteApiKeyAction,
} from '../state/apiKey/actions';
import { EXCHANGES } from '../constants/exchanges';

// binance key hZXMogJFrVQD1NeN6ysO9mycdYfXufatR6YNxyOfNXiukdadmpda0kMA7Pm9wazp
// secret Ip1Rfp33QAlCxBvCS2pek0Mfaul760gUCcisC6GlG9eg7ZDvTVYeV3ezmLqBansJ

type BotUsage = {
    id: string;
    name: string;
    status: string;
};

const ApiKeysPage = () => {
    const dispatch = useAppDispatch();
    const [keys, setKeys] = useState<ApiKey[]>([]);
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [usageInfo, setUsageInfo] = useState<ApiKeyUsageResponse | null>(null);
    const [showPassword, setShowPassword] = useState<boolean>(false);
    const [exchange, setExchange] = useState<string>(EXCHANGES[0].key);
    const [newApiKey, setNewApiKey] = useState<string>('');
    const [apiSecret, setApiSecret] = useState<string>('');
    const [selectedKeyId, setSelectedKeyId] = useState<string | null>(null);

    const togglePasswordVisibility = useCallback((): void => {
        setShowPassword((prev) => !prev);
    }, []);

    const fetchAllApiKeys = useCallback(async (): Promise<void> => {
        setIsLoading(true);
        try {
            const result = await dispatch(getApiKeysAction()).unwrap();
            setKeys(result.apiKeys);
        } catch (e) {
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    }, [dispatch]);

    useEffect(() => {
        fetchAllApiKeys();
    }, [fetchAllApiKeys]);

    const resetForm = useCallback((): void => {
        setExchange('');
        setNewApiKey('');
        setApiSecret('');
    }, []);

    const handleAddKey = useCallback(
        async (e: FormEvent<HTMLFormElement>): Promise<void> => {
            e.preventDefault();
            try {
                setIsLoading(true);
                if (exchange && newApiKey && apiSecret) {
                    const result = await dispatch(
                        addApiKeyAction({
                            exchange,
                            apiKey: newApiKey,
                            apiSecret,
                        })
                    );
                    if (addApiKeyAction.fulfilled.match(result)) {
                        fetchAllApiKeys();
                        resetForm();
                    }
                }
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, exchange, newApiKey, apiSecret, fetchAllApiKeys, resetForm]
    );

    const handleDeleteKey = useCallback(
        async (keyId: string): Promise<void> => {
            try {
                setIsLoading(true);
                const usageResult = await dispatch(
                    getApiKeyUsageAction({ keyId })
                ).unwrap();
                if (!usageResult.canDelete) {
                    setSelectedKeyId(keyId);
                    setUsageInfo(usageResult);
                    return;
                }
                if (
                    window.confirm(
                        'Are you sure you want to delete this API key?'
                    )
                ) {
                    const result = await dispatch(
                        deleteApiKeyAction({ keyId })
                    ).unwrap();
                    if (result.success) {
                        fetchAllApiKeys();
                    }
                }
            } catch (e) {
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, fetchAllApiKeys]
    );

    const closeUsageModal = useCallback((): void => {
        setSelectedKeyId(null);
        setUsageInfo(null);
    }, []);

    return (
        <div className="space-y-8">
            <div>
                <h1 className="text-2xl font-bold text-black">
                    API Key Management
                </h1>
                <p className="text-accent">
                    Securely manage your exchange API keys here.
                </p>
            </div>

            {/* Add API Key Form */}
            <div className="bg-white p-6 rounded-lg shadow-subtle border border-border">
                <div className="flex justify-between items-center mb-4">
                    <h2 className="text-lg font-semibold text-black">
                        Add New API Key
                    </h2>
                </div>
                <form onSubmit={handleAddKey} className="space-y-4">
                    <div>
                        <label
                            htmlFor="exchange"
                            className="block text-sm font-medium text-black mb-1"
                        >
                            Exchange
                        </label>
                        <select
                            id="exchange"
                            value={exchange}
                            onChange={(e) => setExchange(e.target.value)}
                            className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
                            required
                        >
                            {EXCHANGES.map((exchange) => (
                                <option
                                    key={exchange.key}
                                    value={exchange.key}
                                >
                                    {exchange.value}
                                </option>
                            ))}
                        </select>
                    </div>
                    <div>
                        <label
                            htmlFor="apiKey"
                            className="block text-sm font-medium text-black mb-1"
                        >
                            API Key
                        </label>
                        <input
                            id="apiKey"
                            type="text"
                            value={newApiKey}
                            onChange={(e) => setNewApiKey(e.target.value)}
                            placeholder="Your public API key"
                            className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
                            required
                        />
                    </div>
                    <div>
                        <label
                            htmlFor="apiSecret"
                            className="block text-sm font-medium text-black mb-1"
                        >
                            API Secret
                        </label>
                        <div className="relative">
                            <input
                                id="apiSecret"
                                type={showPassword ? 'text' : 'password'}
                                value={apiSecret}
                                onChange={(e) => setApiSecret(e.target.value)}
                                placeholder="Your secret API key"
                                className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
                                required
                            />
                            <button
                                type="button"
                                onClick={togglePasswordVisibility}
                                className="absolute inset-y-0 right-0 pr-3 flex items-center mt-1 cursor-pointer"
                            >
                                {showPassword ? (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        strokeWidth={1.5}
                                        stroke="currentColor"
                                        className="w-5 h-5 text-gray-500"
                                    >
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            d="M3.98 8.223A10.477 10.477 0 001.934 12C3.226 16.338 7.244 19.5 12 19.5c.993 0 1.953-.138 2.863-.395M6.228 6.228A10.45 10.45 0 0112 4.5c4.756 0 8.773 3.162 10.065 7.498a10.523 10.523 0 01-4.293 5.774M6.228 6.228L3 3m3.228 3.228l3.65 3.65m7.894 7.894L21 21m-3.228-3.228l-3.65-3.65m0 0a3 3 0 10-4.243-4.243m4.242 4.242L9.88 9.88"
                                        />
                                    </svg>
                                ) : (
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        fill="none"
                                        viewBox="0 0 24 24"
                                        strokeWidth={1.5}
                                        stroke="currentColor"
                                        className="w-5 h-5 text-gray-500"
                                    >
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            d="M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z"
                                        />
                                        <path
                                            strokeLinecap="round"
                                            strokeLinejoin="round"
                                            d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                                        />
                                    </svg>
                                )}
                            </button>
                        </div>
                    </div>
                    <div className="flex justify-between">
                        <button
                            type="button"
                            onClick={resetForm}
                            className="px-4 py-2 bg-gray-500 text-white font-semibold rounded-lg hover:bg-gray-600 transition-all"
                        >
                            Reset
                        </button>
                        <button
                            type="submit"
                            disabled={isLoading}
                            className="px-4 py-2 bg-black text-white font-semibold rounded-lg hover:bg-opacity-90 disabled:bg-opacity-50 transition-all"
                        >
                            {isLoading ? 'Adding...' : 'Add Key'}
                        </button>
                    </div>
                </form>
            </div>

            {/* List of API Keys */}
            <div className="bg-white p-6 rounded-lg shadow-subtle border border-border">
                <h2 className="text-lg font-semibold text-black mb-4">
                    Your API Keys
                </h2>
                {isLoading && keys.length === 0 ? (
                    <p>Loading keys...</p>
                ) : keys.length === 0 ? (
                    <p className="text-accent">
                        You have not added any API keys yet.
                    </p>
                ) : (
                    <div className="overflow-x-auto">
                        <table className="min-w-full divide-y divide-border">
                            <thead className="bg-secondary">
                                <tr>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider"
                                    >
                                        Exchange
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider whitespace-nowrap"
                                    >
                                        API Key
                                    </th>
                                    <th
                                        scope="col"
                                        className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider whitespace-nowrap"
                                    >
                                        Created At
                                    </th>
                                    <th
                                        scope="col"
                                        className="relative px-6 py-3"
                                    >
                                        <span className="sr-only">Actions</span>
                                    </th>
                                </tr>
                            </thead>
                            <tbody className="bg-white divide-y divide-border">
                                {keys.map((key: ApiKey) => (
                                    <tr key={key._id}>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-black">
                                            {key.exchange}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-accent font-mono">
                                            {`${key.apiKey.substring(0, 8)}...`}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-sm text-accent">
                                            {new Date(key.createdAt).toLocaleDateString()}
                                        </td>
                                        <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                            <button
                                                onClick={() => handleDeleteKey(key._id)}
                                                disabled={isLoading}
                                                className="text-red-600 hover:text-red-900 disabled:text-gray-400"
                                            >
                                                Delete
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>

            {/* Usage Modal */}
            {selectedKeyId && usageInfo && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white p-6 rounded-lg max-w-md w-full mx-4">
                        <h3 className="text-lg font-semibold text-black mb-4">
                            Cannot Delete API Key
                        </h3>
                        <p className="text-accent mb-4">
                            This API key is currently being used by the following bots:
                        </p>
                        <div className="mb-4">
                            {usageInfo.botsUsingKey.map((bot: BotUsage) => (
                                <div
                                    key={bot.id}
                                    className="flex justify-between items-center py-2 border-b border-border last:border-b-0"
                                >
                                    <span className="text-black font-medium">
                                        {bot.name}
                                    </span>
                                    <span
                                        className={`px-2 py-1 rounded text-xs ${
                                            bot.status === 'running'
                                                ? 'bg-green-100 text-green-800'
                                                : bot.status === 'stopped'
                                                ? 'bg-gray-100 text-gray-800'
                                                : 'bg-red-100 text-red-800'
                                        }`}
                                    >
                                        {bot.status}
                                    </span>
                                </div>
                            ))}
                        </div>
                        <p className="text-accent mb-4">
                            Please delete or update these bots to use a different API key before deleting this one.
                        </p>
                        <div className="flex justify-between">
                            <Link
                                to="/bots"
                                className="px-4 py-2 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-all"
                            >
                                Manage Bots
                            </Link>
                            <button
                                onClick={closeUsageModal}
                                className="px-4 py-2 bg-black text-white font-semibold rounded-lg hover:bg-opacity-90 transition-all"
                            >
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default ApiKeysPage;