import { combineReducers } from '@reduxjs/toolkit';
import { persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import userReducer from './auth/reducer';
import emailReducer from './email/reducer';
import usersReducer from './users/reducer';
import apiKeysReducer from './apiKey/reducer';
import botReducer from './bot/reducer';
import filesReducer from './files/reducer';
import subscriptionReducer from './subscription/reducer';
import supportTicketReducer from './supportTicket/reducer';
import referralsReducer from './referrals/reducer';
import notificationsReducer from './notifications/reducer';

const userPersistConfig = {
    key: 'user-root',
    storage,
};

const rootReducer = combineReducers({
    user: persistReducer(userPersistConfig, userReducer),
    email: emailReducer,
    users: usersReducer,
    apiKeys: apiKeysReducer,
    bot: botReducer,
    files: filesReducer,
    subscription: subscriptionReducer,
    supportTicket: supportTicketReducer,
    referrals: referralsReducer,
    notifications: notificationsReducer
});

export default rootReducer;
