import { useEffect, useState, useCallback } from 'react';
import { useAppDispatch, useAppSelector } from '../state/hooks';
import { RootState } from '../state/store';
import {
    getReferralLeaderboardAction,
    getReferralRewardHistoryAction,
    getReferralStatsAction,
} from '../state/referrals/actions';
import {
    ReferralRewardHistory,
    ReferralLeaderboardItem,
    ReferralStats,
} from '../state/referrals/types';
import { UserState } from '../state/auth/types';

const ReferralsPage = () => {
    const dispatch = useAppDispatch();
    const user = useAppSelector((state: RootState) => state.user) as UserState;

    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [history, setHistory] = useState<ReferralRewardHistory[]>([]);
    const [leaderboard, setLeaderboard] = useState<ReferralLeaderboardItem[]>([]);
    const [stats, setStats] = useState<ReferralStats | undefined>(undefined);

    const referralLink = `${window.location.origin}/auth?ref=${user.referralCode}`;

    const handleCopy = useCallback((): void => {
        navigator.clipboard.writeText(referralLink);
    }, [referralLink]);

    const fetchReferralRewardHistory = useCallback(async (): Promise<void> => {
        try {
            setIsLoading(true);
            const historyResult = await dispatch(
                getReferralRewardHistoryAction()
            ).unwrap();
            setHistory(historyResult);
        } catch (e) {
            setHistory([]);
            // eslint-disable-next-line no-console
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    }, [dispatch]);

    const fetchReferralLeaderboard = useCallback(async (): Promise<void> => {
        try {
            setIsLoading(true);
            const leaderboardResult = await dispatch(
                getReferralLeaderboardAction()
            ).unwrap();
            setLeaderboard(leaderboardResult);
        } catch (e) {
            setLeaderboard([]);
            // eslint-disable-next-line no-console
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    }, [dispatch]);

    const fetchReferralStats = useCallback(async (): Promise<void> => {
        try {
            setIsLoading(true);
            const statsResult = await dispatch(
                getReferralStatsAction()
            ).unwrap();
            setStats(statsResult);
        } catch (e) {
            setStats(undefined);
            // eslint-disable-next-line no-console
            console.error(e);
        } finally {
            setIsLoading(false);
        }
    }, [dispatch]);

    useEffect(() => {
        fetchReferralRewardHistory();
    }, [fetchReferralRewardHistory]);

    useEffect(() => {
        fetchReferralLeaderboard();
    }, [fetchReferralLeaderboard]);

    useEffect(() => {
        fetchReferralStats();
    }, [fetchReferralStats]);

    return (
        <div className="bg-white rounded-xl shadow-subtle p-8 border border-border flex flex-col items-center min-h-[300px] w-full max-w-2xl mx-auto">
            <h2 className="text-2xl font-bold mb-4">Referrals</h2>
            <div className="mb-6 w-full flex flex-col items-center">
                <div className="mb-2 text-accent">Share your referral link:</div>
                <div className="flex items-center gap-2 w-full justify-center">
                    <input
                        type="text"
                        value={referralLink}
                        readOnly
                        className="px-3 py-2 rounded border border-border bg-secondary text-black w-full max-w-xs text-sm"
                    />
                    <button
                        onClick={handleCopy}
                        className="px-3 py-2 rounded bg-black text-white text-sm font-semibold hover:bg-gray-800"
                    >
                        Copy
                    </button>
                </div>
                <div className="mt-2 text-xs text-gray-500">
                    Or use code: &nbsp;
                    <span className="font-mono bg-gray-100 px-2 py-1 rounded">
                        {user.referralCode}
                    </span>
                </div>
            </div>
            <div className="mb-6 w-full">
                <div className="font-semibold mb-2">Your Referral Rewards:</div>
                <div className="text-2xl font-bold text-green-600">
                    {user.referralRewards || 0}
                </div>
                <div className="text-xs text-gray-500">
                    Rewarded for each successful referral.
                </div>
            </div>
            <div className="w-full mb-8">
                <div className="font-semibold mb-2">Users you referred:</div>
                {user.referrals && user.referrals.length > 0 ? (
                    <ul className="divide-y divide-border bg-secondary rounded-lg">
                        {user.referrals.map((ref: { _id: string; name: string; email: string }) => (
                            <li
                                key={ref._id}
                                className="py-2 px-4 flex flex-col md:flex-row md:items-center md:justify-between"
                            >
                                <span className="font-medium">{ref.name}</span>
                                <span className="text-xs text-gray-500">{ref.email}</span>
                            </li>
                        ))}
                    </ul>
                ) : (
                    <div className="text-accent text-sm">No referrals yet.</div>
                )}
            </div>
            <div className="w-full mb-8">
                <div className="font-semibold mb-2">Leaderboard: Top Referrers</div>
                {leaderboard.length > 0 ? (
                    <div className="overflow-x-auto">
                        <table className="min-w-full text-sm border rounded-lg">
                            <thead>
                                <tr className="bg-secondary">
                                    <th className="px-3 py-2 text-left">Rank</th>
                                    <th className="px-3 py-2 text-left">Name</th>
                                    <th className="px-3 py-2 text-left">Email</th>
                                    <th className="px-3 py-2 text-left">Rewards</th>
                                </tr>
                            </thead>
                            <tbody>
                                {leaderboard.map((item: ReferralLeaderboardItem, idx: number) => (
                                    <tr key={item._id} className="border-t">
                                        <td className="px-3 py-2">{idx + 1}</td>
                                        <td className="px-3 py-2">{item.name}</td>
                                        <td className="px-3 py-2">{item.email}</td>
                                        <td className="px-3 py-2 font-bold">{item.rewardCount}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                ) : (
                    <div className="text-accent text-sm">No leaderboard data yet.</div>
                )}
            </div>
            <div className="w-full mb-8">
                <div className="font-semibold mb-2">Referral Program Stats</div>
                {stats ? (
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-4">
                        <div className="bg-secondary rounded-lg p-4 text-center">
                            <div className="text-2xl font-bold text-green-600">
                                {stats.totalCompleted}
                            </div>
                            <div className="text-xs text-gray-500 mt-1">
                                Completed Rewards
                            </div>
                        </div>
                        <div className="bg-secondary rounded-lg p-4 text-center">
                            <div className="text-2xl font-bold text-yellow-600">
                                {stats.totalPending}
                            </div>
                            <div className="text-xs text-gray-500 mt-1">
                                Pending Rewards
                            </div>
                        </div>
                        <div className="bg-secondary rounded-lg p-4 text-center">
                            <div className="text-2xl font-bold text-red-600">
                                {stats.totalRevoked}
                            </div>
                            <div className="text-xs text-gray-500 mt-1">
                                Revoked Rewards
                            </div>
                        </div>
                        <div className="bg-secondary rounded-lg p-4 text-center">
                            <div className="text-2xl font-bold text-blue-600">
                                {stats.totalReferrers}
                            </div>
                            <div className="text-xs text-gray-500 mt-1">
                                Total Referrers
                            </div>
                        </div>
                    </div>
                ) : (
                    <div className="text-accent text-sm">Loading stats...</div>
                )}
            </div>
            <div className="w-full">
                <div className="font-semibold mb-2">Referral Reward History:</div>
                {isLoading ? (
                    <div className="text-accent">Loading...</div>
                ) : history.length > 0 ? (
                    <div className="overflow-x-auto">
                        <table className="min-w-full text-sm border rounded-lg">
                            <thead>
                                <tr className="bg-secondary">
                                    <th className="px-3 py-2 text-left">Date</th>
                                    <th className="px-3 py-2 text-left">Referred User</th>
                                    <th className="px-3 py-2 text-left">Reward</th>
                                    <th className="px-3 py-2 text-left">Status</th>
                                    <th className="px-3 py-2 text-left">Description</th>
                                </tr>
                            </thead>
                            <tbody>
                                {history.map((item: ReferralRewardHistory) => (
                                    <tr key={item._id} className="border-t">
                                        <td className="px-3 py-2">
                                            {new Date(item.date).toLocaleDateString()}
                                        </td>
                                        <td className="px-3 py-2">
                                            {item.referredUser && typeof item.referredUser !== 'string'
                                                ? item.referredUser.name
                                                : ''}
                                            <span className="text-xs text-gray-500">
                                                {item.referredUser && typeof item.referredUser !== 'string'
                                                    ? `(${item.referredUser.email})`
                                                    : ''}
                                            </span>
                                        </td>
                                        <td className="px-3 py-2">+{item.rewardAmount}</td>
                                        <td className="px-3 py-2">
                                            <span
                                                className={`inline-block px-2 py-1 rounded text-xs font-semibold ${
                                                    item.status === 'completed'
                                                        ? 'bg-green-100 text-green-700'
                                                        : item.status === 'pending'
                                                        ? 'bg-yellow-100 text-yellow-700'
                                                        : 'bg-red-100 text-red-700'
                                                }`}
                                            >
                                                {item.status}
                                            </span>
                                        </td>
                                        <td className="px-3 py-2">{item.description}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                ) : (
                    <div className="text-accent text-sm">No reward history yet.</div>
                )}
            </div>
        </div>
    );
};

export default ReferralsPage;