import React, { useCallback } from "react";
import { useNavigate } from "react-router-dom";

const Home = () => {
    const navigate = useNavigate();

    const handleClick = useCallback((): void => {
        navigate("/auth");
    }, [navigate]);

    return (
        <div className="min-h-screen flex flex-col p-4">
            <div
                className="flex flex-grow flex-col items-center justify-center gap-6 md:gap-8"
            >
                <h1
                    className="text-3xl md:text-4xl font-bold text-black text-center"
                >
                    Trading Bot Platform
                </h1>
                <p
                    className="text-base md:text-lg text-accent text-center"
                >
                    Automate your trading with style and security.
                </p>
                <button
                    className="px-6 py-3 rounded-lg bg-black text-white font-semibold shadow-subtle hover:shadow-strong transition-all"
                    onClick={handleClick}
                >
                    Get Started
                </button>
            </div>
        </div>
    );
};

export default Home;