import { useCallback, useEffect, useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../state/hooks';
import { RootState } from '../state/store';
import {
    createCheckoutSessionAction,
    createPaypalPaymentAction,
    executePaypalPaymentAction,
} from '../state/subscription/actions';
import { getMeAction } from '../state/auth/actions';
import { PlanTypes } from '../state/subscription/types';

interface PlanItem {
    name: string;
    price: string;
    priceId: string;
    features: string[];
}

const PLANS: PlanItem[] = [
    {
        name: 'Basic',
        price: '$15/mo',
        priceId: 'price_1RiJ5H3T5FbZuPOddsdVLvUA',
        features: [
            'Up to 2 bots',
            'Standard support',
            'Basic analytics',
        ],
    },
    {
        name: 'Premium',
        price: '$30/mo',
        priceId: 'price_1RiJ5b3T5FbZuPOdd324LU1I',
        features: [
            'Up to 12 bots',
            'Priority support',
            'Advanced analytics',
            'Backtesting',
        ],
    },
];

const SubscriptionPage = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const [searchParams] = useSearchParams();
    const user = useAppSelector((state: RootState) => state.user);
    const [isStripeLoading, setIsStripeLoading] = useState<boolean>(false);
    const [isPaypalLoading, setIsPaypalLoading] = useState<boolean>(false);
    const [selectedPlan, setSelectedPlan] = useState<PlanTypes>(user?.subscriptionPlan);

    useEffect(() => {
        const shouldRefresh = searchParams.get('refresh') === 'true';
        if (shouldRefresh) {
            try {
                const result = dispatch(getMeAction());
                if (getMeAction.fulfilled.match(result)) {
                    navigate('/subscription');
                }
            } catch (e) {
                console.error('Error refreshing subscription status:', e);
            }
        }
    }, [searchParams, dispatch, navigate]);

    useEffect(() => {
        const paypalStatus = searchParams.get('paypal');
        const paymentId = searchParams.get('paymentId');
        const PayerID = searchParams.get('PayerID');
        const plan = searchParams.get('plan') as PlanTypes;
        if (
            paypalStatus === 'success' &&
            paymentId &&
            PayerID &&
            plan
        ) {
            try {
                dispatch(
                    executePaypalPaymentAction({ paymentId, PayerID, plan })
                );
                navigate('/subscription');
            } catch (e) {
                console.error(e);
            }
        }
    }, [searchParams, dispatch, navigate]);

    useEffect(() => {
        const sessionId = searchParams.get('session_id');
        if (sessionId) {
            dispatch(getMeAction());
        }
    }, [searchParams, dispatch]);

    const handleStripeSubscribe = useCallback(
        async (planName: PlanTypes, priceId: string) => {
            try {
                setIsStripeLoading(true);
                setSelectedPlan(planName);
                const result = await dispatch(
                    createCheckoutSessionAction({ priceId })
                ).unwrap();
                window.open(result.url, '_self');
            } catch (e) {
                console.error(e);
            } finally {
                setIsStripeLoading(false);
            }
        },
        [dispatch]
    );

    const handlePaypalSubscribe = useCallback(
        async (planName: PlanTypes) => {
            try {
                setIsPaypalLoading(true);
                setSelectedPlan(planName);
                const result = await dispatch(
                    createPaypalPaymentAction({ plan: planName })
                ).unwrap();
                window.open(result.url, '_self');
            } catch (e) {
                console.error(e);
            } finally {
                setIsPaypalLoading(false);
            }
        },
        [dispatch]
    );

    const isSubscribed =
        user?.subscriptionStatus === 'active' ||
        user?.subscriptionStatus === 'trialing';

    return (
        <div className="max-w-4xl mx-auto py-12 px-4">
            <div className="text-center">
                <h1 className="text-3xl font-bold mb-4">Subscription Plans</h1>
                <p className="text-accent mb-8">
                    Choose the plan that&apos;s right for you.
                </p>
            </div>

            {isSubscribed && (
                <div className="text-center mb-10">
                    <p className="text-sm text-accent mt-2">
                        You are currently on the {user.subscriptionPlan} plan.
                    </p>
                </div>
            )}

            <div className="flex flex-wrap justify-center gap-8">
                {PLANS.map((plan) => (
                    <div
                        key={plan.name}
                        className={`bg-white rounded-xl shadow-lg px-10 py-6 flex flex-col items-center ${
                            user?.subscriptionPlan === plan.name
                                ? 'border border-solid border-[#c1c1c1]'
                                : ''
                        }`}
                    >
                        <h2 className="text-xl font-semibold mb-2">{plan.name}</h2>
                        <div className="text-3xl font-bold mb-4">{plan.price}</div>
                        <ul className="flex-grow mb-6 space-y-2 text-gray-700">
                            {plan.features.map((feature) => (
                                <li key={feature} className="flex items-center">
                                    <span className="mr-2 text-green-500">&#10003;</span>
                                    {feature}
                                </li>
                            ))}
                        </ul>
                        <div className="flex flex-col space-y-2">
                            <button
                                className="bg-blue-600 hover:bg-blue-700 text-white font-semibold px-6 py-2 rounded-lg transition disabled:opacity-50"
                                disabled={
                                    (isStripeLoading && selectedPlan === plan.name) ||
                                    user?.subscriptionPlan === plan.name
                                }
                                onClick={() =>
                                    handleStripeSubscribe(
                                        plan.name as PlanTypes,
                                        plan.priceId
                                    )
                                }
                            >
                                {isStripeLoading && selectedPlan === plan.name
                                    ? 'Processing...'
                                    : 'Subscribe with Stripe'}
                            </button>
                            <button
                                className="bg-yellow-500 hover:bg-yellow-600 text-black font-semibold px-6 py-2 rounded-lg transition disabled:opacity-50"
                                disabled={
                                    (isPaypalLoading && selectedPlan === plan.name) ||
                                    user?.subscriptionPlan === plan.name
                                }
                                onClick={() =>
                                    handlePaypalSubscribe(plan.name as PlanTypes)
                                }
                            >
                                {isPaypalLoading && selectedPlan === plan.name
                                    ? 'Processing...'
                                    : 'Subscribe with PayPal'}
                            </button>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default SubscriptionPage;