import {
    useState,
    useCallback,
    ChangeEvent,
    FormEvent
} from 'react';
import { useAppDispatch } from '../state/hooks';
import Button from '../components/ui/Button';
import LoadingSpinner from '../components/ui/LoadingSpinner';
import {
    LineChart,
    Line,
    XAxis,
    YAxis,
    Tooltip,
    ResponsiveContainer,
    CartesianGrid,
    ReferenceDot
} from 'recharts';
import {
    MovingAverageBacktest,
    RSIBacktest,
    BacktestBotRequest,
    BacktestBotResponse
} from '../state/bot/types';
import { backtestBotAction } from '../state/bot/actions';

const defaultMAParams: Partial<MovingAverageBacktest> = {
    symbol: 'BTCUSDT',
    shortPeriod: 10,
    longPeriod: 20,
    quantity: 0.001,
    initialBalance: 1000,
    interval: '1h',
    limit: 200,
    exchange: 'binance',
};

const defaultRSIParams: Partial<RSIBacktest> = {
    symbol: 'BTCUSDT',
    period: 14,
    overbought: 70,
    oversold: 30,
    quantity: 0.001,
    initialBalance: 1000,
    interval: '1h',
    limit: 200,
    exchange: 'binance',
};

type StrategyType = 'moving_average' | 'rsi';

type ParamsType = Partial<MovingAverageBacktest> | Partial<RSIBacktest>;

const getEquityCurve = (
    trades: BacktestBotResponse['trades']
): { time: number; balance: number }[] => {
    return trades.reduce<{ time: number; balance: number }[]>(
        (acc, trade) => {
            acc.push({ time: trade.time, balance: trade.balance });
            return acc;
        },
        []
    );
};

const BacktestingPage = () => {
    const dispatch = useAppDispatch();

    const [strategy, setStrategy] = useState<StrategyType>('moving_average');
    const [params, setParams] = useState<ParamsType>(defaultMAParams);
    const [result, setResult] = useState<BacktestBotResponse | undefined>(undefined);
    const [isLoading, setIsLoading] = useState<boolean>(false);

    const handleStrategyChange = useCallback(
        (e: ChangeEvent<HTMLSelectElement>) => {
            const value = e.target.value as StrategyType;
            setStrategy(value);
            setResult(undefined);
            setParams(value === 'moving_average' ? defaultMAParams : defaultRSIParams);
        },
        []
    );

    const handleChange = useCallback(
        (e: ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
            setParams((prev: ParamsType) => ({ ...prev, [e.target.name]: e.target.value }));
        },
        []
    );

    const handleNumberChange = useCallback(
        (e: ChangeEvent<HTMLInputElement>) => {
            setParams((prev: ParamsType) => ({ ...prev, [e.target.name]: parseFloat(e.target.value) }));
        },
        []
    );

    const handleSubmit = useCallback(
        async (e: FormEvent<HTMLFormElement>) => {
            e.preventDefault();
            setIsLoading(true);
            setResult(undefined);
            try {
                let request: BacktestBotRequest;
                if (strategy === 'moving_average') {
                    const maParams = params as Partial<MovingAverageBacktest>;
                    request = {
                        strategy: 'moving_average',
                        symbol: String(maParams.symbol),
                        shortPeriod: Number(maParams.shortPeriod),
                        longPeriod: Number(maParams.longPeriod),
                        quantity: Number(maParams.quantity),
                        initialBalance: Number(maParams.initialBalance),
                        interval: String(maParams.interval),
                        limit: Number(maParams.limit),
                        exchange: String(maParams.exchange),
                    };
                } else {
                    const rsiParams = params as Partial<RSIBacktest>;
                    request = {
                        strategy: 'rsi',
                        symbol: String(rsiParams.symbol),
                        period: Number(rsiParams.period),
                        overbought: Number(rsiParams.overbought),
                        oversold: Number(rsiParams.oversold),
                        quantity: Number(rsiParams.quantity),
                        initialBalance: Number(rsiParams.initialBalance),
                        interval: String(rsiParams.interval),
                        limit: Number(rsiParams.limit),
                        exchange: String(rsiParams.exchange),
                    };
                }
                const res = await dispatch(backtestBotAction(request)).unwrap();
                setResult(res);
            } catch (e) {
                setResult(undefined);
                // eslint-disable-next-line no-console
                console.error(e);
            } finally {
                setIsLoading(false);
            }
        },
        [dispatch, strategy, params]
    );

    const equityCurve = result?.trades ? getEquityCurve(result.trades) : [];

    return (
        <div className="bg-white rounded-xl shadow-subtle p-8 border border-border max-w-3xl mx-auto mt-8">
            <h2 className="text-2xl font-bold mb-4">Backtesting</h2>
            <p className="text-accent mb-6">
                Run and review strategy backtests here. Powered by real Binance historical data.
            </p>
            <form onSubmit={handleSubmit} className="mb-4 md:mb-8">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Strategy</label>
                        <select
                            name="strategy"
                            value={strategy}
                            onChange={handleStrategyChange}
                            className="w-full px-3 py-2 border rounded-md"
                        >
                            <option value="moving_average">Moving Average</option>
                            <option value="rsi">RSI</option>
                        </select>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Exchange</label>
                        <select
                            name="exchange"
                            value={params.exchange}
                            onChange={handleChange}
                            className="w-full px-3 py-2 border rounded-md"
                        >
                            <option value="binance">Binance</option>
                            <option value="coinbase">Coinbase</option>
                            <option value="kraken">Kraken</option>
                        </select>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Symbol</label>
                        <input
                            name="symbol"
                            value={params.symbol}
                            onChange={handleChange}
                            className="w-full px-3 py-2 border rounded-md"
                            required
                        />
                    </div>
                    {strategy === 'moving_average' && (
                        <>
                            <div>
                                <label className="block text-sm font-medium text-black mb-1">Short MA Period</label>
                                <input
                                    name="shortPeriod"
                                    type="number"
                                    min={1}
                                    value={(params as Partial<MovingAverageBacktest>).shortPeriod ?? ''}
                                    onChange={handleNumberChange}
                                    className="w-full px-3 py-2 border rounded-md"
                                    required
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-black mb-1">Long MA Period</label>
                                <input
                                    name="longPeriod"
                                    type="number"
                                    min={1}
                                    value={(params as Partial<MovingAverageBacktest>).longPeriod ?? ''}
                                    onChange={handleNumberChange}
                                    className="w-full px-3 py-2 border rounded-md"
                                    required
                                />
                            </div>
                        </>
                    )}
                    {strategy === 'rsi' && (
                        <>
                            <div>
                                <label className="block text-sm font-medium text-black mb-1">RSI Period</label>
                                <input
                                    name="period"
                                    type="number"
                                    min={1}
                                    value={(params as Partial<RSIBacktest>).period ?? ''}
                                    onChange={handleNumberChange}
                                    className="w-full px-3 py-2 border rounded-md"
                                    required
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-black mb-1">Overbought Level</label>
                                <input
                                    name="overbought"
                                    type="number"
                                    min={1}
                                    max={100}
                                    value={(params as Partial<RSIBacktest>).overbought ?? ''}
                                    onChange={handleNumberChange}
                                    className="w-full px-3 py-2 border rounded-md"
                                    required
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-black mb-1">Oversold Level</label>
                                <input
                                    name="oversold"
                                    type="number"
                                    min={1}
                                    max={100}
                                    value={(params as Partial<RSIBacktest>).oversold ?? ''}
                                    onChange={handleNumberChange}
                                    className="w-full px-3 py-2 border rounded-md"
                                    required
                                />
                            </div>
                        </>
                    )}
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Quantity</label>
                        <input
                            name="quantity"
                            type="number"
                            min={0.00001}
                            step={0.00001}
                            value={params.quantity}
                            onChange={handleNumberChange}
                            className="w-full px-3 py-2 border rounded-md"
                            required
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Initial Balance ($)</label>
                        <input
                            name="initialBalance"
                            type="number"
                            min={1}
                            value={params.initialBalance}
                            onChange={handleNumberChange}
                            className="w-full px-3 py-2 border rounded-md"
                            required
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Interval</label>
                        <select
                            name="interval"
                            value={params.interval}
                            onChange={handleChange}
                            className="w-full px-3 py-2 border rounded-md"
                        >
                            <option value="1m">1m</option>
                            <option value="5m">5m</option>
                            <option value="15m">15m</option>
                            <option value="1h">1h</option>
                            <option value="4h">4h</option>
                            <option value="1d">1d</option>
                        </select>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-black mb-1">Data Points</label>
                        <input
                            name="limit"
                            type="number"
                            min={50}
                            max={1000}
                            value={params.limit}
                            onChange={handleNumberChange}
                            className="w-full px-3 py-2 border rounded-md"
                            required
                        />
                    </div>
                </div>
                <div className="flex justify-end">
                    <Button type="submit" disabled={isLoading}>
                        {isLoading ? <LoadingSpinner /> : 'Run Backtest'}
                    </Button>
                </div>
            </form>
            {result && (
                <div className="mt-8">
                    <h3 className="text-lg font-bold mb-2">Results</h3>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
                        <div className="bg-gray-50 rounded-lg p-4 text-center">
                            <div className="text-xs text-gray-500">Final Balance</div>
                            <div className="text-xl font-bold text-black">
                                ${result.finalBalance.toFixed(2)}
                            </div>
                        </div>
                        <div className="bg-gray-50 rounded-lg p-4 text-center">
                            <div className="text-xs text-gray-500">Total PnL</div>
                            <div
                                className={`text-xl font-bold ${
                                    result.pnl >= 0 ? 'text-green-600' : 'text-red-600'
                                }`}
                            >
                                {result.pnl >= 0 ? '+' : ''}${result.pnl.toFixed(2)}
                            </div>
                        </div>
                        <div className="bg-gray-50 rounded-lg p-4 text-center">
                            <div className="text-xs text-gray-500">Win Rate</div>
                            <div className="text-xl font-bold text-black">
                                {(result.winRate * 100).toFixed(1)}%
                            </div>
                        </div>
                    </div>
                    <div className="mb-8">
                        <h4 className="text-md font-semibold mb-2">Equity Curve</h4>
                        <ResponsiveContainer width="100%" height={300}>
                            <LineChart
                                data={equityCurve}
                                margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
                            >
                                <CartesianGrid strokeDasharray="3 3" />
                                <XAxis
                                    dataKey="time"
                                    tickFormatter={(t: number) =>
                                        new Date(t).toLocaleDateString()
                                    }
                                />
                                <YAxis domain={['auto', 'auto']} />
                                <Tooltip
                                    labelFormatter={(t: number) =>
                                        new Date(t).toLocaleString()
                                    }
                                />
                                <Line
                                    type="monotone"
                                    dataKey="balance"
                                    stroke="#2563eb"
                                    strokeWidth={2}
                                    dot={false}
                                />
                                {result.trades.map((trade, idx) => (
                                    <ReferenceDot
                                        key={idx}
                                        x={trade.time}
                                        y={trade.balance}
                                        r={4}
                                        fill={
                                            trade.side === 'buy'
                                                ? '#22c55e'
                                                : '#ef4444'
                                        }
                                        stroke="#fff"
                                    />
                                ))}
                            </LineChart>
                        </ResponsiveContainer>
                    </div>
                    <div>
                        <h4 className="text-md font-semibold mb-2">Trade Log</h4>
                        <div className="overflow-x-auto">
                            <table className="min-w-full divide-y divide-border">
                                <thead className="bg-secondary">
                                    <tr>
                                        <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">
                                            Time
                                        </th>
                                        <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">
                                            Side
                                        </th>
                                        <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">
                                            Price
                                        </th>
                                        <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">
                                            Quantity
                                        </th>
                                        <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">
                                            Balance
                                        </th>
                                    </tr>
                                </thead>
                                <tbody className="bg-white divide-y divide-gray-100">
                                    {result.trades.map((trade, idx) => (
                                        <tr
                                            key={idx}
                                            className="hover:bg-blue-50 transition"
                                        >
                                            <td className="px-4 py-2 text-xs text-gray-500">
                                                {new Date(trade.time).toLocaleString()}
                                            </td>
                                            <td
                                                className={`px-4 py-2 font-semibold ${
                                                    trade.side === 'buy'
                                                        ? 'text-green-600'
                                                        : 'text-red-600'
                                                }`}
                                            >
                                                {trade.side.toUpperCase()}
                                            </td>
                                            <td className="px-4 py-2">
                                                ${trade.price.toFixed(2)}
                                            </td>
                                            <td className="px-4 py-2">
                                                {trade.quantity}
                                            </td>
                                            <td className="px-4 py-2">
                                                ${trade.balance.toFixed(2)}
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default BacktestingPage;