import { makeApiCall, handleActionResponse, handleActionError } from '../global/api';
import type {
    ApiError,
    ActionResponse,
} from '../global/types';
import type {
    GetBotsResponse,
    GetBotLogsResponse,
    GetPaperTradesResponse,
    GetPaperTradingStatsResponse,
    BotTypes,
    PaperTradeConfig,
    BacktestBotRequest,
    BacktestBotResponse,
    BacktestBotStrategyRequest,
    UpdateBotApiKeyRequest,
    GetBotStatusResponse,
    GetBotOpenPositionsResponse,
    GetUserTradesResponse
} from './types';

export const getAllBotsService = async (
    queries?: { page?: string; limit?: string; }
): Promise<ActionResponse<GetBotsResponse>> => {
    try {
        const response = await makeApiCall<GetBotsResponse>(
            '/api/bots',
            'GET',
            { params: queries }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

export const createBotService = async (
    data: Partial<BotTypes>
): Promise<ActionResponse<BotTypes>> => {
    try {
        const response = await makeApiCall<BotTypes>(
            '/api/bots',
            'POST',
            { data }
        );
        return handleActionResponse(response);
    } catch (error) {
        return handleActionError(error as ApiError);
    }
};

